<template>
    <modal name="CurrentStoreSettingsModal"        
        width="30%"
        height="auto"
        @before-open="beforeOpen"
    >
        <transition name="modal">
            <div class="currentStoreSettings-modal-mask">
                <div class="currentStoreSettings-modal-wrapper">
                    <div class="currentStoreSettings-modal-container">

                        <div id="modalTitle" class="modal__title">
                            <p >Appointment Type</p>
                        </div>

                        <div  class="modal__subtitle">
                            <p>For {{currentStore.Name}}</p>
                        </div>

                        <v-container class="bloublou" fluid>
                          <v-checkbox v-model="selectedTypes" label="In store appointment" value="inStore" class="m" on-icon="mdi-checkbox-marked" off-icon="mdi-checkbox-blank-outline"></v-checkbox>
                          <v-checkbox v-model="selectedTypes" label="Virtual appointment" value="distant" on-icon="mdi-checkbox-marked" off-icon="mdi-checkbox-blank-outline"></v-checkbox>
                          <!-- <md-checkbox v-model="selectedTypes" value="inStore"> <p class="checkbox_label"> In store appointment</p> </md-checkbox>
                          <md-checkbox v-model="selectedTypes" value="distant"> <p class="checkbox_label"> Virtual appointment</p> </md-checkbox> -->
                        </v-container>

                        <div class="currentStoreSettings-modal-footer">
                              <button @click="closeModal()" class="eventTypeBtn cancel-btn">
                                Cancel 
                              </button>
                              <button @click="confirm()" class="eventTypeBtn">
                                <div v-if="!showLoader"> Confirm </div>
                                <div class="lds-spinner" v-if="showLoader"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
                              </button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

    </modal>
</template>

<script>
import utils from '../commons/utils'
export default {
  name: 'CurrentStoreSettingsModal',
  components: {
    
  },
  data () {
    return {
        showLoader: false,
        title: "Store Settings",
        types: [
          { label: 'In store', value: 'inStore' },
          { label: 'Virtual', value: 'distant' },
        ],
        selectedTypes:[],
        currentStore:{}
    }
  },

  methods:{
      beforeOpen(event){
      this.currentStore = this.$store.state.storeInfos;
      console.log(this.currentStore)
      this.selectedTypes = this.currentStore.AppointmentType__c ? this.currentStore.AppointmentType__c.split(";") : ["inStore"];
      console.log(this.selectedTypes);
      this.title = this.currentStore.Name
    },
      closeModal(){
        this.$modal.hide('CurrentStoreSettingsModal');
      },
      confirm(){
        console.log(this.selectedTypes)        
          utils.updateStoreSettings(this.currentStore.Id, this.selectedTypes.join(";"), this.saved)
      },
      saved() {
        this.currentStore.AppointmentType__c = this.selectedTypes.join(";");
        this.$store.dispatch('setStoreInfos',  this.currentStore);
        this.closeModal()
      }
  },
  computed:{
  },
     
}
</script>
<style scoped>
.checkbox_label{
  padding-left: 10px;
  font-size: 18px ;
}
.md-checkbox {
    display: flex;
  }

.currentStoreSettings-modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .1);
  display: table;
  transition: opacity .3s ease;
}

.currentStoreSettings-modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.currentStoreSettings-modal-container {
  width: 479px;
  margin: 0px auto;
  padding: 34px 79px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  text-align: center;
}

.currentStoreSettings-modal-body {
  font-size: 17px;
  font-weight: 300;
  line-height: 1.18;
  color: #000000;
  margin-bottom: 32px
}

.currentStoreSettings-modal-footer {
    display: flex;
    flex-wrap: nowrap;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

  .eventTypeBtn{
    max-width: 130px;
    padding: 12px 30px;
  }
  .cancel-btn{
    background-color: #eae8e4;
    color: #000;
  }
  .multiselect-label{
    text-align: left;
  }
  .modal__title > p{
    font-size: 22px;
    font-weight: bold;
  }

  .modal__subtitle > p{
    font-size: 14px;
    color: #929292;
    margin-top: -15px;
  }

  .white_checkbox {
        background-color: #ffffff !important;
    }

  .v-input--selection-controls{
      margin-top: 3px !important;
    }
  .v-input--selection-controls .v-input__slot{
      background-color: #ffffff !important;
    }



</style>

<style>
  .bloublou{
        padding: 25px 0 45px;
  }
  .bloublou .v-input__slot {
    background-color: #ffffff;
    margin-bottom: 0!important;
  }
  .bloublou .v-messages{
    min-height: 0;
  }
</style>


