<template>
    <div class="list-wrapper"  @scroll="onScroll">
        <div class="list-container">
            <template v-if="filteredEvents">
                <!-- <template v-for="(group, groupName) in groups">
                    <div :class="closestDate() == msToDate(groupName) ? 'present list-date' : 'list-date'" :key="groupName + Math.random()">{{msToWeekDay(parseInt(groupName)) + ', ' + msToMonth(parseInt(groupName)) + ' ' + msToDay(parseInt(groupName))}}</div>
                        <div :key="item + Math.random()" v-for="item in group">
                            <div :class=" 'list-event ' + onlineAppointmentClassTag(item) + ' ' + assignedClassTag(item) + ' ' + availabilityClassTag(item)" @click="open(item)"
                                :key="item.OwnerId + Math.random()">
                                <div class="list-event-time">{{msToTime(item.localStoreStartDateTime) + ' - ' + msToTime(item.localStoreEndDateTime)}}</div>
                                <div class="list-event-name">{{item.accountName}}</div>
                                <div class="list-event-sa"><img class="list-owner-name-img" :src="getPictureUrl(item)" />{{item.ownerName}}</div>
                                <div class="list-event-status" style="width: 41px"></div>
                                 <div class="list-event-availability">{{item.freeCapacities + ' available slots'}}</div>
                            </div>
                        </div>
                </template> -->
                <template v-if="eventsMap">
                  <template v-for="date  in Object.keys(eventsMap)">
                    <div :class="closestDate() == date ? 'present list-date' : 'list-date'" :key="date + Math.random()">{{msToGoodFormatDay(datesMap[date])}}</div>
                    <template v-for="(value, name) in eventsMap[date]">
                      <template v-for="(event,index) in value">
                        <template >
                          <span :key="event.id + Math.random()" v-show="showThisElement(event)">
                            <div :class=" 'list-event ' + onlineAppointmentClassTag(event) + ' ' + assignedClassTag(event) + ' ' + availabilityClassTag(event)" @click="open(event)"
                                :key="event.OwnerId + Math.random()">
                                <div class="list-event-time">{{getLocaleHour(event.gmtStartDateTime) + ':00 - ' + getLocaleHour(event.gmtEndDateTime) + ':00'}}</div>
                                <div class="list-event-availability">{{event.freeCapacities + ' available slots'}}</div>
                                <div class="list-event-name">{{event.accountName ? event.accountName : " "}}</div>
                                <div class="list-event-sa"><img class="list-owner-name-img" :src="getPictureUrl(event)" />{{event.ownerName}}</div>
                                <div class="list-event-status-wrap" v-if="isVirtualEnabled">
                                  <img src="@/assets/icone.svg" v-show="event.appointmentType == 'distant'" class="virtual_label" />
                                  <div class="list-event-status" style="width: 41px"></div>
                                </div>

                            </div>
                        </span>
                        </template>
                      </template>
                    </template>
                  </template>
                </template>
            </template>    
        </div>   
    </div>
</template>

<script>
import moment from 'moment'
import utils from '../commons/utils'
export default {
  name: 'ListView',
  components: {
    
  },
  data () {
    return {
      scrolledToBottom: false,
      currentRange:{},
      datesMap:{}
    }
  },
  methods:{
      open (event) {
      console.group('open',event);

      if(event.recordType == 'Availability'){
        this.$emit('showAvailToEdit', event);
      }
      else{
        this.$emit('showAssignModal', event);
      }
    },
    groupBy(array, key){
        const result = {}
        array.forEach(item => {
            if (!result[item[key]]){
            result[item[key]] = []
            }
            result[item[key]].push(item)
        })
        return result
    },
    // groupBy(array, key) {
    //   const result = {};
    //   let sortable = [];
    //   array.forEach(item => {
    //     if (!result[item[key]]) {
    //       result[item[key]] = [];
    //     }
    //     result[item[key]].push(item);
    //   });

    //   for (var i in result) {
    //     sortable.push([i, result[i]]);
    //   }
    //   sortable.sort(function(a, b) {
    //     return a[1] - b[1];
    //   });
    //   return sortable;
    // },
    msToDate(ms){
        return moment(ms).tz(this.storeInfos.Time_Zone_Id__c).format('YYYY-MM-DD');
    },
    msToDayZ(ms){
      return moment(ms).format('DD');
    },

    msToTime(ms){
      var d = new Date(ms);
      return d.getHours() + ':00';
    },
    msToWeekDay(ms){
      var d = new Date(ms);
      let realDate = new Date(ms).toLocaleString('en-us', {weekday: 'short'});
      return realDate;
    },
    msToMonth(ms){
      var d = new Date(ms);
      let realDate = new Date(ms).toLocaleString('en-us', {month: 'short'});
      return realDate;
    },
    msToDay(ms){
      var d = new Date(ms);
      return d.getDate();
    },
    msToGoodFormatDay(ms){
      return moment(ms).tz(this.storeInfos.Time_Zone_Id__c).format('ddd, MMM DD ');
    },
    onlineAppointmentClassTag(item){
      if (item.recordType == "Online_Appointment"){
        return "online-appointment-tag"
      }
      if (item.recordType == "Appointments"){
        return "appointment-tag"
      }
    },
    assignedClassTag(item){
      if (item.status == "Assigned"){
        return "assigned-tag"
      }
      if(item.status == "Not Assigned"){
        return "unassigned-tag"
      }
      if(item.status == "Cancelled"){
        return "cancelled-tag"
      }
    },
    availabilityClassTag(item){
      if (item.recordType == "Availability"){
        return "availability-tag"
      }
      else{
        return "not-availability-tag"
      }
    },
    getPictureUrl(item){
        let filterUser = this.availableTeamUsers.filter(ev => ev.Id == item.ownerId)
        let userUrl = '';
        if(filterUser[0])
          userUrl = filterUser[0].FullPhotoUrl
        return userUrl
    },
    arrayUnique(array) {
        var a = array.concat();
        for(var i=0; i<a.length; ++i) {
            for(var j=i+1; j<a.length; ++j) {
                if(a[i] === a[j])
                    a.splice(j--, 1);
            }
        }
        return a;
    },
    getLocaleHour(datems){
        // console.log(datems)
        let localHour = null;
        if(datems){
            localHour = moment(datems).tz(this.storeInfos.Time_Zone_Id__c).format('HH');
        }
        // console.log(localHour)
        return localHour
      },
    showThisElement(event){
      if(event.recordType == 'Availability' && event.freeCapacities <= 0){
        return false;
      } else {
        return true;
      }
    },
    closestDate(){
      // const now = new Date();
      // let closest = Infinity;

      // Object.keys(this.eventsMap).forEach(function(d) {
      //   var ddate = new Date(d);
      //   let fullDate = ddate.getFullYear() + '-' + ("0" + (ddate.getMonth() + 1)).slice(-2) + '-' + ("0" + ddate.getDate()).slice(-2);

      //   const date = new Date(fullDate);
      //   if (date >= now && (date < new Date(closest) || date < closest)) {
      //       closest = fullDate;
      //   }
      // });

      // return closest;

      var testDate = new Date();
      var daysO = Object.keys(this.eventsMap);
      var days = [];

      for(var i=0; i <daysO.length; i++){
        days.push(new Date(daysO[i]))
      }

      var bestDate = days.length;
      var bestDiff = -(new Date(0,0,0)).valueOf();
      var currDiff = 0;
      var i;

      for(i = 0; i < days.length; ++i){
        currDiff = Math.abs(days[i] - testDate);
        if(currDiff < bestDiff){
            bestDate = i;
            bestDiff = currDiff;
        }   
      }
      /* the best date will be days[bestDate] */
      let dateToFormat = days[bestDate];

      if(dateToFormat){
        return  dateToFormat.getFullYear() + '-' + ("0" + (dateToFormat.getMonth() + 1)).slice(-2) + '-' + ("0" + dateToFormat.getDate()).slice(-2);
      } else {
        return new Date();
      }
      
    },
    getMoreEvents() {
      let actualRange = {}
      actualRange.start = this.currentRange.end;
      actualRange.end = moment(this.currentRange.end).add(7, 'days').format('YYYY-MM-DD');

      //this.$store.dispatch('updateActualRange', actualRange);
        
      utils.getDateRangeAvailabilities(true, actualRange);
      utils.getOnlineAndTeamAppointments('infinite', actualRange);
      this.currentRange = actualRange
    },
    onScroll ({ target: { scrollTop, clientHeight, scrollHeight }}) {
      if (scrollTop + clientHeight >= scrollHeight) {
        this.getMoreEvents()
        //console.log('bottom')
      }
    },
    getLocaleDate(datems){
        //console.log(datems)
      let localDate;
      if(datems && this.storeTZ){
        localDate = moment(datems).tz(this.storeTZ).format('YYYY-MM-DD');
      }
      //console.log(localDate)
      return localDate
    },
    getStatusPriority(event){
      if(event.recordType == 'Online_Appointment' && event.status == 'Not Assigned'){
        return 1;
      } else if(event.recordType == 'Online_Appointment' && event.status == 'Assigned'){
        return 2;
      } else if(event.recordType == 'Appointments'){
        return 3;
      } else {
        return 4;
      }
    },
  },
  mounted(){
    let actualRange = {}
    actualRange.start = moment().add(-7, 'days').format('YYYY-MM-DD');
    actualRange.end = moment().add(7, 'days').format('YYYY-MM-DD');
     utils.getDateRangeAvailabilities(true, actualRange);
    utils.getOnlineAndTeamAppointments('infinite', actualRange);
    this.currentRange = actualRange
    this.$scrollTo('.present')
  },
  computed: {
    events(){
        return this.$store.state.events
    },
    availableTeamUsers(){
        return this.$store.state.availableTeamUsers;
    },
    groups(){
        return this.groupBy(this.filteredEvents, 'gmtStartDateTime')
    },
    selectedEventsFilters(){
        return this.$store.state.selectedEventsFilters;
    },
     filteredEvents(){
        if(this.selectedEventsFilters.indexOf('All') != -1){
            return this.events.filter(event => event.status != "Cancelled" );
        } else{
            let filteredEventsArr = [];
            for(var i = 0; i<this.selectedEventsFilters.length;i++){
            if(this.selectedEventsFilters[i] == "Available"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Availability" && event.freeCapacities >= 0)));
            }
            if(this.selectedEventsFilters[i] == "Unassigned"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Online_Appointment" && event.status == "Not Assigned")));
            }
            if(this.selectedEventsFilters[i] == "Assigned"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Online_Appointment" && event.status == "Assigned")));
            }
            if(this.selectedEventsFilters[i] == "ICON"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Appointments")));
            }
            }
            return filteredEventsArr;
        }
    },
    storeInfos(){
      return this.$store.state.storeInfos;
    },
    storeTZ(){
        return this.$store.state.storeInfos.Time_Zone_Id__c;
    },
    isUserActionAuthorized(){
        return utils.isUserActionAuthorized();
    },
    // actualDateRangeToShow(){
    //   let actualDateRange = this.$store.state.actualDateRange
    //   return actualDateRangeToShow
    // },
    // getListDateHeaders(){
    //  let actualDateRange = this.$store.state.actualDateRange;
    // //  let startDateObj = new Date(actualDateRange.start);
    //  let getListDateHeaders = [];
    //  for (let index = 0; index < 8; index++) {
    //     let startDateObj = new Date(actualDateRange.start);
    //     if (index != 0 ){
    //     startDateObj.setDate(startDateObj.getDate() + index) ;
    //     }
    //     let day = startDateObj.toLocaleString('en-us', {  weekday: 'short' });
    //     let month = startDateObj.toLocaleString('en-us', {  month: 'short' });
    //     let dayNum = startDateObj.getDate();
    //     getListDateHeaders.push(day + ', ' + month + ' ' + dayNum );
    //  }
    //  return getListDateHeaders;
    // },
    eventsMap () {
        // const map = {}

        let datesMap2 = {}
        if(this.filteredEvents.length > 0){
          let group = this.filteredEvents.reduce((r, a) => {
          var d = new Date();
          if(a.gmtStartDateTime){
            d = new Date(a.gmtStartDateTime);
          } else if(a.localStoreStartDateTime){
            d = new Date(a.localStoreStartDateTime);
          }

            let tempDate = this.getLocaleDate(d);
            let tempHour = d.getHours() + ':00';
            r[tempDate] = [...r[tempDate] || [], a];
            return r;
            }, {});

            let group2 = {}
            Object.keys(group).forEach(key=>{
              group2[key] = group[key].reduce((r, a) => {
            
            datesMap2[key] = a.gmtStartDateTime;

            var d = new Date();
            if(a.gmtStartDateTime){
              d = new Date(a.gmtStartDateTime);
            } else if(a.localStoreStartDateTime){
              d = new Date(a.localStoreStartDateTime);
            }

            let tempDate = this.getLocaleDate(d);
            let tempHour = d.getHours() + ':00';
            r[tempHour] = [...r[tempHour] || [], a];
            r[tempHour] = r[tempHour].sort((a,b) => {
               return (this.getStatusPriority(a) < this.getStatusPriority(b)) ? 1 : -1
               })
            return r;
            }, {});
            })
          this.$store.dispatch('setEventsMap', group2)
          console.log(group2)
          this.datesMap = datesMap2
          return group2
        }
        
      },
      todayDate(){
        var d = new Date();
        return d.getFullYear() + '-' + ("0" + (d.getMonth() + 1)).slice(-2) + '-' + ("0" + d.getDate()).slice(-2);
      },
      isVirtualEnabled(){
        return this.$store.state.isVirtualEnabled;
      },
  }
}
</script>
<style>
@import url('../../static/img/assets/LVFont/font-faces.css');

.list-wrapper{
    padding: 12px 43px 19px 29px;
    background-color: #f7f8fb;
    overflow-y: auto;
    height: 81vh;
}
  
.list-container{
    display: flex;
    flex-direction: column;
    margin-bottom: 37px
}

.list-date{
    font-size: 16px;
    color: #000000;
    margin-top: 20px;
    margin-bottom: 14px;
    text-transform: uppercase;
    font-weight: 600;
}

.list-event{
    border-radius: 7px;
    box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.09);
    background-color: #ffffff;
    white-space: nowrap;
    font-size: 13px;
    cursor: pointer;
    display: flex;
    padding: 12px 30px;
    align-items: center;
    margin-bottom: 12px;
    justify-content: space-between;
}

.list-event-time{
    font-size: 14px;
    font-weight: 500;
    color: #768191;
    max-width: 100px;
    margin-right: 15px;
}

.list-event-name{
    font-size: 16px;
    color: #2e3a49;
    max-width: 200px;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list-event-sa{
    opacity: 0.81;
    font-size: 16px;
    font-weight: 400;
    color: #000000;
    max-width: 200px;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list-event-status-wrap{
  display: flex;
  align-items: center;
}
.list-event-status-wrap img{
  margin-right: 8px;
}
.list-event-status::after{
    content: 'ONLINE';
    border-radius: 2px;
    background-color: #7a91ff;
    color: #ffffff;
    font-size: 8px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    padding: 2px 4px;
    letter-spacing: 1px;
    display: inline;
    font-family: LouisVuitton-Bold;
}

.list-event-empty{
    border-radius: 7px;
    background-color: #f1f2f9;
    color: #797979;
    font-size: 16px;
    text-transform: uppercase;
    padding: 12px;
    text-align: center
}

.event-unassigned{
    border-top: dashed 1px #e30041;
    border-right: dashed 1px #e30041;
    border-bottom: dashed 1px #e30041;
    border-left: 6px #e30041 solid;
}

.availability-tag{
  background-color: #ebeefd;
    color: #54698d;
    font-size: 13px;
    border-left: 6px #c2cbf5 solid;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.list-owner-name-img{
    min-width: 20px;
    max-width: 20px;
    height: 20px;
    margin-bottom: -5px;
    margin-right: 12px;
    border-radius: 100%;
}

.list-event-availability{
    font-size: 16px;
}

.availability-tag .list-event-name,  .availability-tag .list-event-status{
  display: none;
}

.not-availability-tag .list-event-availability{
  display: none;
}

.appointment-tag .list-event-status::before{
    display: none;
}

.list-event-type{
  display: flex;
  align-items: center;
  /* padding-left: 3px; */
  position: relative;
  right: 3px;
}
.list-event-type{
  visibility: visible;
}



.user_label{
      display: flex;
    flex-direction: row;
}

.tooltip-label{
  visibility: hidden;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 3px 8px;
  border-radius: 3px;
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translate(-50%, -100%);
  top: 0;
}
.tooltip-label:after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.virtual_label {
  width: 13px;
  height: 13px;
  position: relative;
}

@media screen and (max-height: 915px) {
  .list-wrapper{
        height: 77.5vh;
  }
}

@media screen and (max-height: 768px) {
  .list-wrapper{
        height: 73.5vh;
  }
}


</style>


