<template>
    <div class="sideBar">
        SideBar
        <button @click="test">
          TEST
        </button>
        <createEventModal />
    </div>
</template>

<script>
import moment from 'moment'
import createEventModal from './CreateEventModal'

export default {
  name: 'SideBar',
  components: {
    createEventModal
  },
  data () {
    return {
      
    }
  },
  methods:{
    test(){
      // var date = moment().date();
      // console.log(date)

      this.$modal.show('createEventModal')
    }
  }
}
</script>
<style>
    .sideBar{
      background-color: #6f5441;
      float: left;
      height: calc(100% - 50px - 70px);
      width: 350px;
    }
</style>


