<template>
    <modal name="createEventModal"        
        class="createEventModal"
        width="30%"
        height="auto"
        @before-open="beforeOpen"
        @closed="closeModal"
    >
        <div class="createEventModalContent">
        <div class="modal-close" @click="closeModal()"></div>
            
            <h3 class="modal-title">Create Slots</h3>

            <v-container grid-list-md text-xs-center class="inputs-container">
                <v-layout row wrap> <!-- row 1 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <!-- <v-icon>event</v-icon> -->
                            <span>Date</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        <v-menu
                            v-model="dateMenu"
                            :close-on-content-click="false"
                            :nudge-right="-10"
                            transition="scale-transition"
                            offset-y
                            full-width
                            min-width="290px"
                        >
                            <template v-slot:activator="{ on }">
                                
                            <v-text-field
                                v-model="start"
                                readonly
                                v-on="on"
                                append-icon="mdi-menu-down"
                            ></v-text-field>
                            </template>
                            <v-date-picker v-model="start" @input="dateMenu = false" color="black" prev-icon="mdi-chevron-left" next-icon="mdi-chevron-right"></v-date-picker>
                            
                        </v-menu>
                        
                    </v-flex>
                </v-layout>

                <v-layout row wrap>  
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <span>Hours</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9 style="display: flex; padding: 4px 0;">
                        <v-flex xs5>
                            <v-select
                            :items="optionsHours"
                            :menu-props="{ bottom: true, offsetY: true }"
                            v-model="timeFrom"
                            filled
                            append-icon="mdi-menu-down"
                            ></v-select>
                        </v-flex>
                        <v-spacer></v-spacer>
                        <span class="toLabel">to</span>
                        <v-spacer></v-spacer>
                        <v-flex xs5>
                            <v-select
                            :items="optionsHours"
                            v-model="timeTo"
                            :menu-props="{ bottom: true, offsetY: true }"
                            filled
                            append-icon="mdi-menu-down"
                            ></v-select>
                    </v-flex>
                    </v-flex>
                </v-layout>

                <v-layout row wrap> <!-- row 3 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <!-- <v-icon>people</v-icon> -->
                            <span>Capacity</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        
                                <v-select
                                :items="capacityOptions"
                                :menu-props="{ bottom: true, offsetY: true }"
                                v-model="chosenCapacity"
                                filled
                                append-icon="mdi-menu-down"
                                ></v-select>
                       
                    </v-flex>
                </v-layout>

                <v-layout row wrap v-if="!showRecurrenceMenu"> <!-- row 3 -->
                        <div class="inputsLabel">
                            <!-- <v-icon>people</v-icon> -->
                            <span>You can only create recurrence for slots of 5 hours maximum.</span>
                        </div> 
                </v-layout>
                <v-layout row wrap v-if="showRecurrenceMenu"> 
                    <v-flex xs3>
                        <div class="inputsLabel">
                    
                            <span>Recurrence</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9 >
                        <v-layout row wrap style="flex-wrap: nowrap;">
                            <v-flex xs5 class="Reccurence-select">
                                <v-select
                                v-model="SelectedRecurrenceType"
                                :items="RecurrenceTypes"
                                item-text = "key"
                                item-value = "value"
                                filled
                                :menu-props="{ bottom: true, offsetY: true }"
                                append-icon="mdi-menu-down"
                                ></v-select>
                            </v-flex>
                        
                            <template  v-if="SelectedRecurrenceType == 'RecursEveryWeekday' || SelectedRecurrenceType == 'RecursWeekly'">
                                <button v-for="{key, value} in days" :key="key" :class="(selectedDayForRecurrence.indexOf(value) != -1 ? 'selectedDay weekDayBtn' : 'weekDayBtn')" @click="dayClicked(value)">{{key}}</button>
                            </template>
                        </v-layout>
                    </v-flex>
                </v-layout>

                <v-layout row wrap style="max-height: 135px;" v-if="showRecurrenceMenu"> 
                    <v-flex xs3>
                        <div class="inputsLabel">
                  
                            <span></span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        
                        <v-radio-group v-model="radios" class="radio-ends" v-show="SelectedRecurrenceType != 'NO'">
                            <div class="radio-ends-row">
                                <v-radio label="Never" value="never" color="black" on-icon="mdi-radiobox-marked" off-icon="mdi-radiobox-blank"></v-radio>
                            </div>
                            <div class="radio-ends-row" >
                                <v-radio label="On" value="on" color="black" on-icon="mdi-radiobox-marked" off-icon="mdi-radiobox-blank"> </v-radio>
                                <v-flex xs6>
                                    <v-menu
                                    v-model="showEndDatePicker"
                                    :close-on-content-click="true"
                                    :nudge-right="-20"
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                        <template v-slot:activator="{ on }">
                                            <v-text-field
                                                v-model="end"
                                                readonly
                                                v-on="on"
                                            ></v-text-field>
                                        </template>
                                        <v-date-picker 
                                            v-model="end" 
                                            :allowed-dates="recurrenceAllowedDates"
                                            show-current="false"
                                            color="black" 
                                            prev-icon="mdi-chevron-left"
                                            next-icon="mdi-chevron-right">
                                        </v-date-picker>
                                    </v-menu>
                                </v-flex>
                            </div>
                    </v-radio-group>
                       
                    </v-flex>
                </v-layout>

            </v-container>
            <div class="footer-btn">
                <button @click="closeModal()" class="eventTypeBtn delete-btn" v-if="isUserActionAuthorized">
                    Cancel
                </button>
                <button @click="saveAvailability(eventType)" class="eventTypeBtn" v-if="isUserActionAuthorized">
                    <div v-if="!showLoader"> Save </div>
                    <div class="lds-spinner" v-if="showLoader == 'loading'">
                        <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
                    </div>
                    <div v-if="showLoader == 'error'">
                        <span>An error occurred</span>
                    </div>
                </button>
            </div>
        </div>        
    </modal>
</template>

<script>
import moment from 'moment';
import 'moment-timezone';
import LCC from 'lightning-container';
import utils from '../commons/utils';

export default {
  name: 'createEventModal',
  data () {
    return {
      showLoader: null,
      type: 'day',
      end: null,
    //   start: new Date().toISOString().substr(0, 10),
      lowestTime: '',
      duration: '',
      event: {},
      eventType: 'Availability',      
      dateMenu: false,
      dateMenuEnds: null,
      timePickerMenuFrom: false,
      timePickerMenuTo: false,
      timeFrom: null,
      timeTo: null,
      modal: false,
      capacityOptions: [1, 2, 3, 4, 5, 6, 7],
      chosenCapacity: 1,
      SelectedRecurrenceType:'NO',
      RecurrenceTypes:[
        {key:"None", value: "NO"},
        {key:"Daily", value: "RecursDaily"},
        // {key:"Every Weekday",value: "RecursEveryWeekday"},
        // {key:"Monthly",value: "RecursMonthly"},
        // {"Daily",value: "RecursMonthlyNth"},
        {key:"Weekly",value: "RecursWeekly"},
        // {key:"Yearly",value: "RecursYearly"},
        // {"Daily" : "RecursYearlyNth"}
      ],
      selectedDayForRecurrence: [],
      showEndDatePicker: false,
      radios: 'never',
      days:[
        {key:"S", value: "1"},
        {key:"M", value: "2"},
        {key:"T",value: "4"},
        {key:"W",value: "8"},
        {key:"TH",value: "16"},
        {key:"F",value: "32"},
        {key:"S",value: "64"},
      ],
      isSelectDay: undefined,
    }
  },
  methods:{
      beforeOpen(event) {
        if(event.params){
            this.lowestTime = event.params.lowestTime;
            this.duration = event.params.duration; 
            this.event = event.params.event; 

            this.timeFrom = this.lowestTime;
            this.timeTo = moment.utc(this.timeFrom,'HH:mm').add(this.duration / 3600000,'hour').format('HH:mm')  
        }
      },
      saveAvailability(eventType){
        if(this.timeFrom && this.timeTo){
            if(this.timeFrom > this.timeTo){
                alert("End hour cannot be before start. Please verify the hours fields");
            } else {
                let durationH = moment.utc(moment(this.timeTo,"HH:mm").diff(moment(this.timeFrom,"HH:mm"))).format("H")
          
                for(var i=0; i<durationH; i++){
                    let eventStartTime = moment(this.timeFrom, "HH:mm").add(i, 'hour').format('HH:mm')
                    let eventEndTimeMS = moment(this.timeFrom, "HH:mm").add(i+1, 'hour').valueOf()
                    // console.log(new Date(moment.tz(this.start + ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).format()))

                    var RecurrenceEndDateOnly = null;
                    var RecurrenceInterval = null;
                    var RecurrenceDayofWeekMask = null; 
                    var IsRecurrence = false;
                    var RecurrenceDayOfMonth = null;
                    var RecurrenceMonthOfYear = null;

                    // no recurrence ?
                    if(this.SelectedRecurrenceType == "NO"){
                        RecurrenceEndDateOnly = moment(this.start).valueOf();
                    } else {
                        IsRecurrence = true;
                    }

                    //calculate end of recurrence
                    if(IsRecurrence && this.radios == "never"){
                        RecurrenceEndDateOnly = (this.start ? moment(this.start).add(this.getMaxEndDayRecurrencesAllowed(), 'days') : moment().add(this.getMaxEndDayRecurrencesAllowed(), 'days')).valueOf();
                        IsRecurrence = true;
                    }
                    else if(IsRecurrence && this.radios == "on"){
                        if(this.end){
                            RecurrenceEndDateOnly = moment(this.end).valueOf();
                        } else {
                            RecurrenceEndDateOnly = (this.start ? moment(this.start).add(this.getMaxEndDayRecurrencesAllowed(), 'days') : moment().add(this.getMaxEndDayRecurrencesAllowed(), 'days')).valueOf();
                        }
                        
                        IsRecurrence = true;
                    }
                    
                    if(this.SelectedRecurrenceType == "RecursDaily"){
                        RecurrenceInterval = 1;
                        IsRecurrence = true;
                    }

                    if(this.SelectedRecurrenceType == "RecursMonthly" || this.SelectedRecurrenceType == "RecursYearly"){
                        RecurrenceDayOfMonth = moment(this.start).day();
                    }

                    if(this.SelectedRecurrenceType == "RecursMonthly" || this.SelectedRecurrenceType == "RecursWeekly"){
                        RecurrenceInterval = 1;
                    }

                    if(this.SelectedRecurrenceType == "RecursEveryWeekday" || this.SelectedRecurrenceType == "RecursWeekly"){
                        var startDay = moment(this.start).day();
                        RecurrenceDayofWeekMask = this.days[startDay].value;
                        if(this.selectedDayForRecurrence.length > 0){
                            RecurrenceDayofWeekMask = this.selectedDayForRecurrence.reduce((a, b) => parseInt(a) + parseInt(b), 0); //sum of the selectedDayForRecurrence array's values 
                        }
                    }

                    if(this.SelectedRecurrenceType == "RecursYearly"){
                        RecurrenceMonthOfYear = moment(this.start).month().format('MMMM');
                    }

                    // if no recurrence, ensure that the call will be properly made
                    if(this.SelectedRecurrenceType == "NO"){
                        RecurrenceEndDateOnly = null;
                        RecurrenceInterval = null;
                        RecurrenceDayofWeekMask = null; 
                        IsRecurrence = false;
                        RecurrenceDayOfMonth = null;
                        RecurrenceMonthOfYear = null;
                    }


                    this.$store.dispatch('addEventsToInsert', 
                        {
                            title: eventType,
                            date: this.start,  
                            time: eventStartTime,
                            DurationInMinutes: 60,
                            definedCapacities:this.chosenCapacity,
                            freeCapacities: this.chosenCapacity,
                            // gmtEndDateTime:eventEndTimeMS,
                            // gmtStartDateTime: new Date(this.start + ' ' + eventStartTime).getUTCMilliseconds(),
                            id: null,
                            localStoreEndDateTime: eventEndTimeMS,
                            // localStoreStartDateTime: new Date(this.start + ' ' + eventStartTime).getTime() - (1*60*60*1000), //substract 1 hour
                            localStoreStartDateTime : new Date(moment.tz(this.start + ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).format()).getTime(),
                            recordType:"Availability",
                            storeCode:this.storeInfos.RetailStoreId__c,
                            storeName:this.storeInfos.Name,
                            timeZone:this.storeInfos.Time_Zone_Id__c,

                            IsRecurrence: IsRecurrence,
                            RecurrenceStartDateTime: new Date(moment.tz(this.start + ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).format()).getTime(),
                            RecurrenceEndDateOnly: RecurrenceEndDateOnly,
                            RecurrenceType: this.SelectedRecurrenceType != 'NO' ? this.SelectedRecurrenceType : null,
                            RecurrenceInterval: RecurrenceInterval,
                            RecurrenceDayofWeekMask: RecurrenceDayofWeekMask,
                            RecurrenceDayOfMonth: RecurrenceDayOfMonth,
                            //RecurrenceTimeZoneSidKey: this.storeInfos.Time_Zone_Id__c,
                        }
                    );

                }

                //create avaiklability in SF
                this.showLoader = 'loading'
                this.createAvailabilityInSF();
                // this.chosenCapacity = 1;
                // this.$modal.hide('createEventModal');
            }
        } else{
            alert("Please fill Hours fields to save your availability");
        }
      },
      closeModal(){
          this.showLoader = null;
          this.$modal.hide('createEventModal');
      },
      makeSelectDay: function() {
          this.isSelectDay = !this.isSelectDay;
      },
      createAvailabilityInSF(){
        if (process.env.NODE_ENV === 'production') {
            //let params = JSON.stringify(this.$store.state.events);

            LCC.callApex(
                'OnlineAppointmentsController.createAvailabilities',
                JSON.stringify(this.$store.state.eventsToInsert),
                (result, event) => {
                if (event.status) {
                    this.handleLCCQueryResponse(result)
                    this.showLoader = null
                    this.$root.$emit('rerenderCal');
                    this.$modal.hide('createEventModal');
                } else if (event.type === 'exception') {
                    console.log(event.message + ' : ' + event.where)
                    this.showLoader = 'error'
                } else {
                    console.log('Unknown Error', event)
                }
                },
                {escape: false}
            )
            this.$store.dispatch('emptyEventsToInsert');

            } else {
            console.log('not in production')
             //this.$modal.hide('createEventModal');
             this.showLoader = 'error';
             this.selectedDayForRecurrence = [];
             this.$store.dispatch('addEvents', this.$store.state.eventsToInsert);
             this.$store.dispatch('emptyEventsToInsert');
            }
            console.log(' ');
        },
        handleLCCQueryResponse(response) {
            // console.log('handleLCCQueryResponse: ', response)
            
            let events = response.createdAvailabilities.map((record)=>{
                return {
                id: record.id,
                recordType: record.recordType, 
                date: record.date,
                localStoreStartDateTime: record.localStoreStartDateTime,
                DurationInMinutes: record.DurationInMinutes,
                storeCode: record.storeCode,
                storeName: record.storeName,
                definedCapacities: record.definedCapacities,
                freeCapacities: record.freeCapacities,
                }
            })

            if (response.existingSlots && response.existingSlots.length > 0) {
                this.$alert(
                   `${response.createdAvailabilities && response.createdAvailabilities.length > 0 ?`Availabilities were saved but ` : `` } your store already has availabilities at the following times:${response.existingSlots.map((rec) => moment.tz(rec, this.storeInfos.Time_Zone_Id__c).format('LLL')).join(', ')}`,
                    '',
                    'warning');
            }

            this.$store.dispatch('addEvents', events);
            utils.getDateRangeAvailabilities(false);

        },
      timePickerAllowedMinutes: m => m % 60 === 0,
      dayClicked(day){
        if(this.selectedDayForRecurrence.indexOf(day) == -1){
          this.selectedDayForRecurrence.push(day)
        } else {
          this.selectedDayForRecurrence.splice(this.selectedDayForRecurrence.indexOf(day), 1)
        }
      },
    //allowedDates: val => parseInt(val.split('-')[2], 10) % 2 === 0,
    recurrenceAllowedDates(val){
        return moment(val) > moment() && moment(val) < (this.start ? moment(this.start).add(this.getMaxEndDayRecurrencesAllowed(), 'days') : moment().add(this.getMaxEndDayRecurrencesAllowed(), 'days'));
    },

    getMaxEndDayRecurrencesAllowed(){
          if(this.SelectedRecurrenceType == 'RecursDaily'){
              return 99;
          } else if (this.SelectedRecurrenceType == 'RecursWeekly'){
              let nbRecurr = this.selectedDayForRecurrence && this.selectedDayForRecurrence.length > 0 ? this.selectedDayForRecurrence.length : 1;
              return Math.floor(-nbRecurr + 53*7/nbRecurr);
          }
      }
 
  },
 computed:{
      nowTime(){
        var d = new Date();
        return d;
      },
      start:{
          get: function () {
            let mddStore = this.$store.state.mouseDownDate;
            if(mddStore == "NaN-aN-aN"){
                mddStore = new Date().toISOString().substr(0, 10)
            }else return mddStore
          },
          set: function (newValue) {
            this.$store.dispatch('updateMouseDownDate', newValue);    
          }
      },
      optionsHours(){
        let n = 24;
        let options = [];
        for (var i=0; i<n; i++) {
          if(i.toString().length == 1){
               i = '0' + i; 
          }
          options.push(i + ':00')
        } 
      return options;
      },
      storeInfos(){
        return this.$store.state.storeInfos;
      },
      isUserActionAuthorized(){
          return utils.isUserActionAuthorized();
      },
      showRecurrenceMenu(){
        var a = ("2017-05-02T" + this.timeFrom);
        var b = ("2017-05-02T" + this.timeTo);
        var milliseconds = ((new Date(a)) - (new Date(b)));
        var minutes = milliseconds / (60000);
        if(Math.abs(minutes) <= 5*60){
            return true;
        } else {
            return false;
        }
      }
      
  } 
}
</script>

<style >
    .createEventModal .v--modal-box.v--modal{
        min-width: 586px;
        margin: auto;
        left: unset !important;
        border-radius: 11px;
    }
    
    .createEventModalContent{
        padding: 42px 31px 51px 48px;
        text-align: right;
        padding-top: 30px;
    }

    .modal-close{
        position: absolute;
        right: 2em;
        top: 2em;
        width: 1.1em;
        height: 1.1em;
        cursor: pointer;
    }

    .modal-close:after, .modal-close:before {
        content: '';
        position: absolute;
        width: 2px;
        height: 1.5em;
        background: #ccc;
        display: block;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        left: 50%;
        margin: -3px 0 0 -1px;
        top: 0;
    }

    .modal-close:hover:after,
    .modal-close:hover:before {
        background: #aaa;
    }

    .modal-close:before{
        transform: rotate(-45deg);
    }

    .inputs-container{
        padding: 0;
        padding-right: 40px
    }

    .modal-title{
        font-size: 27px;
        text-align: left
    }
    .eventTypeSelect{
        width: 100px;
        border: 1px solid black;
        padding-left: 10px;
    }
    .eventTypeBtn{
        background-color: #000000;
        color: #ffffff;
        font-size: 15px;
        min-width: 158px;
        min-height: 46px;
        outline: none;
        margin-top: 10px;
        margin-right: 12px
    }
    .delete-btn{
        background-color: #eae8e4;
        color: #000000;
        margin-right: 12px;
         padding: 12px 45px;
    }
    .inputsLabel{
        text-align: left;
        padding-top: 17px;
        opacity: 0.5;
        font-size: 18px;
        line-height: 1.22;
        color: #000000;
    }

    .layout.row {
        align-items: baseline;
        max-height: 70px;
    }

    .v-input__slot {
        min-height: 48px;
        background-color: #f6f5f3;
        padding-left: 19px;
    }

    .v-input__slot:before, .v-input__slot:after{
        display: none;
    }

    .v-text-field .v-input__append-inner{
        align-self: auto;
    }

    .capacityText{
        text-align: left;
        padding-left: 15px !important;
        padding-top: 25px !important;
    }

    .week-day-wrapper{
        display: flex;
        flex-wrap: nowrap
    }

    .week-day-wrapper::before{
        content: "Repeat on";
        position: absolute;
        top: 43%;
        color: #8c9199;
    }

    .radio-ends::before{
        content: "Ends";
        position: absolute;
        top: 61.5%;
        font-size: 14px;
        color: #8c9199;
        margin-bottom: 20px;
    }

    .Reccurence-select {
        margin-right: 10px;
        text-align: left;
    }

    /* .Reccurence-select::before{
        content: "Repeat every";
        position: absolute;
        top: 43%;
        color: #8c9199;
    } */

    .week-day button {
        padding: 2px 6px;
        height: 26px;
        width: 35px;
        min-width: unset;
        margin: auto 5px auto 0;
        box-shadow: none;
        border-radius: 5px;
        background-color: #f9f8f6;
        color: #949494;
        outline: none;
        cursor: pointer;
    }

    .selectedDay{
        background-color: #d9d7d2 !important;
        color: #000000 !important;
    }

    .v-radio {
        min-width: 57px;
    }

    .radio-ends .v-input__slot{
        padding-left: 0;
        background: none
    }

    .radio-ends-row{
        display: flex;
        height: 34px;
    }

    .radio-ends-row .v-input__slot{
        background-color: #f6f5f3;
        padding-left: 19px;
        min-height: 40px
    }

    .radio-ends-row .v-text-field{
        margin-top: -20px;
    }


    .radio-input .v-input__slot{
        width: 43px;
        padding-left: 12px;
    }

    label.v-label {
        color: #000000 !important;
        font-size: 17px;
    }

    .toLabel{
        margin: auto;
        opacity: 0.5;
        font-size: 18px;
    }

    .footer-btn{
        display: flex;
        justify-content: flex-end;
    }

    @media screen and (max-width: 1024px) {
      .createEventModal .v--modal-box.v--modal{
        min-width: 510px;
        top: 60px !important;
        margin: auto !important;
        height: auto !important;
        left: 0 !important;
      }
      
      .createEventModalContent {
        padding: 27px 16px 27px 33px;
      }

      .radio-ends::before{
        top: 59%;
      }
    }

    .weekDayBtn{
        border: 1px solid #adadad;
        border-radius: 5px;
        margin: 2px;
        width: 24px;
    }
</style>


