<template>
    <modal name="confirmRecurringModeModal"        
        width="30%"
        height="auto"
    >
        <transition name="modal">
            <div class="recurring-modal-mask">
                <div class="recurring-modal-wrapper">
                    <div class="recurring-modal-container">

                      <div class="recurring-modal-title">
                          {{title &&  title != "" ? title : "Recurring slot" }}
                        </div>
                          <v-radio-group v-model="mode" :mandatory="true">
                            <v-radio off-icon="mdi-radiobox-blank" on-icon="mdi-radiobox-marked" label="Only this slot" value="CURRENT"></v-radio>
                            <v-radio off-icon="mdi-radiobox-blank" on-icon="mdi-radiobox-marked" label="This slot and the following ones" value="CURRENT_AND_MORE"></v-radio>
                            <v-radio off-icon="mdi-radiobox-blank" on-icon="mdi-radiobox-marked" label="All slots" value="ALL"></v-radio>
                          </v-radio-group>
                        <div class="recurring-modal-footer">
                             <button @click="closeModal()" class="eventTypeBtn recurring-btn">
                                Cancel 
                            </button>
                            <button @click="confirmRecurringModeSelect()" class="eventTypeBtn" v-if="isUserActionAuthorized">
                               <div v-if="!showLoader"> OK </div>
                            <div class="lds-spinner" v-if="showLoader"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>

                            </button>
                        </div>
                        
                    </div>
                </div>
            </div>
        </transition>

    </modal>
</template>

<script>
import utils from '../commons/utils'

export default {
  name: 'confirmRecurringModeModal',
  components: {
    
  },
  props:['title'],
  data () {
    return {
        mode:"CURRENT",
        showLoader: null

    }
  },
  methods:{
      closeModal(){
        // this.$root.$emit('rerenderCal');
        this.$modal.hide('confirmRecurringModeModal');        
      },
      confirmRecurringModeSelect(){
        this.showLoader = true
        this.$emit('confirmRecurringModeSelect', this.mode);
        // this.$root.$emit('rerenderCal');
    }
  },
  computed:{
      isUserActionAuthorized(){
          return utils.isUserActionAuthorized();
      },
  },
     
}
</script>
<style>

.recurring-modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .1);
  display: table;
  transition: opacity .3s ease;
}

.recurring-modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.recurring-modal-container {
  width: 479px;
  margin: 0px auto;
  padding: 34px 79px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  text-align: center;
}

.recurring-modal-body {
  font-size: 17px;
  font-weight: 300;
  line-height: 1.18;
  color: #000000;
  margin-bottom: 32px
}

.recurring-modal-title {
  font-size: 22px;
  font-weight: 600;
  line-height: 1.18;
  color: #000000;
  margin-bottom: 32px
}

.recurring-modal-footer {
    display: flex;
    flex-wrap: nowrap;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

    .lds-spinner {
    color: official;
    /* display: inline-block; */
    position: relative;
    bottom: 16px;
    left: 60px;
    /* width: 80px;
    height: 80px; */
    }
    .lds-spinner div {
    transform-origin: 15px 17px;
    animation: lds-spinner 1.2s linear infinite;
    }
    .lds-spinner div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 14px;
    width: 2px;
    height: 9px;
    border-radius: 20%;
    background: #fff;
    }
    .lds-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
    }
    .lds-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
    }
    .lds-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
    }
    .lds-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
    }
    .lds-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
    }
    .lds-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
    }
    .lds-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
    }
    .lds-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
    }
    .lds-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
    }
    .lds-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
    }
    .lds-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
    }
    .lds-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
    }
    @keyframes lds-spinner {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
    }

</style>


