<template>
    <modal name="onBoardingModal"        
        width="30%"
        height="auto"
    >
        <transition name="modal">
            <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">

                        <div class="modal-header">
                            Schedule Available Slots
                        </div>

                        <div class="modal-body">
                            Welcome, <span style="font-weight: 600">{{userInfos.FirstName ? userInfos.FirstName : ''}}!</span> <br> Please schedule your upcoming month in order to give visibility to your clients on LV.com. 
                        </div>

                        <div class="modal-img">
                            <span class="modal-img-title">Drag to create slots</span>
                            <img class="modal-img-box" src="@/assets/bitmap.png" />
                        </div>

                        <div class="modal-footer">
                            <button class="modal-default-button" @click="$emit('close')">
                                Got it
                            </button>
                        </div>
                        
                    </div>
                </div>
            </div>
        </transition>

    </modal>
</template>

<script>

export default {
  name: 'onBoardingModal',
  components: {
    
  },
  data () {
    return {

    }
  },
  computed:{
     storeInfos(){
      return this.$store.state.storeInfos;
    },
    userInfos(){
      return this.$store.state.userInfos;
    },
  }
     
}
</script>
<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .1);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 479px;
  margin: 0px auto;
  padding: 34px 79px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  text-align: center;
}

.modal-header {
  font-size: 24px;
  font-weight: bold;
  color: #000000;
  margin-bottom: 19px;
  text-transform: uppercase
}

.modal-body {
  font-size: 17px;
  font-weight: 300;
  line-height: 1.18;
  color: #000000;
  margin-bottom: 32px
}

.modal-img{
    margin-bottom: 31px;
    display: flex;
    flex-direction: column;
}

.modal-img-title{
  font-size: 12px;
  color: #797979;
  margin-bottom: 13px;
}

.modal-img-box{
  width: 156px;
  height: 106px;
  display: inline-block;
  margin: 0 auto;
}

.modal-default-button {
    width: 233px;
    height: 46px;
    background-color: #19110b;
    cursor: pointer;
    font-size: 15px;
    color: #ffffff
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

</style>


