<template>
    <div class="header-bar">
      <div class="calendar-date" v-if="calendarType != 'list'">
        

          <v-btn @click="$root.$emit('prevCalendar')" id="calendar-btn-left">
            <v-icon
              dark
              left
            >
              mdi-chevron-left
            </v-icon>
          </v-btn>
        

          <div class="calendar-date-content">
            {{actualDateRangeToShow}}
          </div>


          <v-btn @click="$root.$emit('nextCalendar')" id="calendar-btn-right">
            <v-icon
              right
              dark
            >
              mdi-chevron-right
            </v-icon>
          </v-btn>
      </div>

      <div class="calendar-date-list" v-else></div>


      <v-tabs 
        v-model="type"
        :value="typeOptions"
        label="Type"
        class="calendarViewTypeSelect"
        @change="typeChanged"
        :grow='true'>
        <template v-for="item in typeOptions">
          <v-tab :key='item'>{{item}}</v-tab>
        </template>
      </v-tabs>


    </div>
</template>

<script>

export default {
  name: 'Header',
  components: {
    
  },
  data () {
    return {
      type: 1,
      start: '2019-01-01',
      end: '2019-01-06',
      typeOptions: ['day', 'week', 'list'],
    }
  },
  methods:{
    typeChanged(value) {
      this.$store.dispatch('updateCalendarViewType', this.typeOptions[value]);        
    },
  },
  computed: {
    actualDateRangeToShow(){
      let actualDateRange = this.$store.state.actualDateRange
      let actualDateRangeToShow = ''
      if(actualDateRange.startDisplayString == actualDateRange.endDisplayString){
        actualDateRangeToShow = actualDateRange.startDisplayString
      } else {
        actualDateRangeToShow = actualDateRange.startDisplayString + " - " + actualDateRange.endDisplayString
      }
      actualDateRangeToShow += ", " + actualDateRange.year
      return actualDateRangeToShow
    },
    calendarType(){
      return this.$store.state.calendarViewType;
    },
  }
}
</script>
<style>
  /* @import url('../assets/LVFont/font-faces.css'); */

  .application{
    font-family: 'Louis Vuitton';
  } 

    .header-bar{
        background-color: #f7f8fb;
        padding: 14px 38px;
        display: flex;
        justify-content: center;
        border-top: 1px grey solid;
    }

    .calendar-date{
      display: flex;
      align-items: end;
    }

    .calendar-date-list{
      height: 36px;
    }

    #calendar-btn-left, #calendar-btn-right{
      background: none;
      box-shadow: none;
      padding: 0;
      height: inherit;
      min-width: inherit;
      margin: 6px 14px;
    }

    .calendar-date-content{
      font-size: 23px;
      font-weight: 500;
    }

    .v-icon--right, .v-icon--left{
      margin: 0;
    }
    
    .calendarViewTypeSelect{
      font-size: 16px;
      width: 262px;
      height: 36px;
      position: absolute;
      right: 36px;
      border-radius: 17px;
    }

    .calendarViewTypeSelect .v-tabs__div{
      text-transform: capitalize;
      font-size: 16px;
      font-weight: 400;
    }

    .calendarViewTypeSelect .v-tabs__container{
      height: 36px;
    }

    .calendarViewTypeSelect .v-tabs__slider{
      height: 0;
    }
    
    .calendarViewTypeSelect .v-tabs__item{
      transition: none;
      border-radius: 17px;
    }

    .calendarViewTypeSelect a.v-tabs__item.v-tabs__item--active {
      color: #fff;
      background-color: #000000;
      border-radius: 17px;
    }

    @media screen and (max-width: 1330px) {
      .header-bar {
          justify-content: flex-start;
      }
    }

    @media screen and (max-width: 1024px) {
      .header-bar {
          padding: 14px 10px 14px 51px;
          justify-content: left;
      }
      .calendar-date-content {
        font-size: 20px;
      }

      .calendarViewTypeSelect {
          width: 178px;
          right: 22px;
          margin-top: 4px;
      }

      #calendar-btn-left, #calendar-btn-right {
        margin: 2px 0px;
      }

      .calendar-date {
        margin-top: 6px;
        align-items: flex-end;
      }

      .calendarViewTypeSelect .v-tabs__container {
        height: 30px;
      }
      
    }

    @media screen and (max-width: 768px) {
     

      .calendar-date-content {
        font-size: 17px;
      }

    }



</style>


