    isUserActionAuthorized(){
      var arr = store.state.authorizedProfiles.user_action__c.toLowerCase().split(',');// ["system administrator", "icon_sa manager", "icon_corporate", "icon_samanager_everywhere", "icon_corporate_everywhere", "iconics_care_expert"];      
      arr = arr.map(Function.prototype.call, String.prototype.trim);
      var isOk = arr.indexOf(this.getUserProfile().toString().toLowerCase()) != -1 ? true : false

      var authorizedPermissionSets = (store.state.authorizedProfiles.user_action_PS__c != null) ? store.state.authorizedProfiles.user_action_PS__c.split(',') : [];
      var isOkPS = store.state.userPermissionSet.some((v)=>authorizedPermissionSets.includes(v));
      return isOk || isOkPS;
    },
    storeChangeAuthorized(){
      var arr = store.state.authorizedProfiles.store_change__c.toLowerCase().split(','); //["system administrator", "icon_corporate", "iconics_corporate", "iconics_sa_manager", "iconics_sa", "icon_corporate_everywhere", "iconics_care_expert"];      
      arr = arr.map(Function.prototype.call, String.prototype.trim);
      var isOk = arr.indexOf(this.getUserProfile().toString().toLowerCase()) != -1 ? true : false
     
      var authorizedPermissionSets = (store.state.authorizedProfiles.store_change_PS__c != null) ? store.state.authorizedProfiles.store_change_PS__c.split(',') : [];
      var isOkPS = store.state.userPermissionSet.some((v)=>authorizedPermissionSets.includes(v));
      return isOk || isOkPS;
    },
