      // WARNING : the profiles in the array will NOT be able to change the appointment type, its en EXCLUSION list
      var arr = store.state.authorizedProfiles.appointment_type_change__c.toLowerCase().split(',');//["iconics_sa", "iconics_care_expert"];      
      arr = arr.map(Function.prototype.call, String.prototype.trim);
      var isOk = arr.indexOf(this.getUserProfile().toString().toLowerCase()) != -1 ? true : false

      var authorizedPermissionSets = (store.state.authorizedProfiles.appointment_type_change_PS__c != null) ? store.state.authorizedProfiles.appointment_type_change_PS__c.split(','):[];
      var isOkPS = store.state.userPermissionSet.some((v)=>authorizedPermissionSets.includes(v));
      return isOk || isOkPS;
    },
    isCSCUser(){
      var arr = store.state.authorizedProfiles.CSC_user__c.toLowerCase().split(',');//["system administrator", "iconics_corporate", "iconics_sa_manager", "iconics_sa", "iconics_care_expert"];      
      arr = arr.map(Function.prototype.call, String.prototype.trim);
      var isOk = arr.indexOf(this.getUserProfile().toString().toLowerCase()) != -1 ? true : false

      var authorizedPermissionSets = (store.state.authorizedProfiles.CSC_user_PS__c != null) ? store.state.authorizedProfiles.CSC_user_PS__c.split(',') : [];
      var isOkPS = store.state.userPermissionSet.some((v)=>authorizedPermissionSets.includes(v));
      return isOk || isOkPS;
    },
    debounce (fn, delay) {
      var timeoutID = null
      return function () {
        clearTimeout(timeoutID)
        var args = arguments
        var that = this
        timeoutID = setTimeout(function () {
          fn.apply(that, args)
        }, delay)
      }
    }
}
</script>


