<template>
  <v-app>
                        <loading :active.sync="isLoading"
             :can-cancel="false"
             :is-full-page="true"
             :height="128"
             :width="128"
             :color="color"
             :loader="loader"
             :background-color="bgColor">
    </loading>
    <v-content v-if="isPilot == true">
      <LeftMenu @calKeyUpdate="incrementCalKey" />
      <div class="horizontal-wrapper">

        <HeaderBar />
        <UsersSelection />
        <Calendar class="calendar-wrapper" ref="calendarComponent" v-if="!isHidden" :key="calKey" @calKeyUpdate="incrementCalKey"/>
        <!-- <div class="calendar-footer">
          <div class="calendar-footer-left">Canceled Appointment</div>
          <div class="calendar-footer-center"><img class="calendar-footer-center-img" src="@/assets/noun-show-1517840.png" />No Show</div>
          <div class="calendar-footer-right"><img class="calendar-footer-right-img" src="@/assets/noun-shop-bag-2664616.png" />Purchase</div>
        </div>   -->
      </div> 
      <onBoardingModal @close="hideOnboarding()" />
    </v-content>
    <v-content v-else> <!-- if user store is not in pilot, show this page -->

      <template v-if="isPilot == false ">
        <div style="text-align:center;width:100%;padding-top: 50px;background-color: white;">
          <div><img style="width:200px;height: auto;" src="@/assets/LV-logo.png" /></div>
          <div>
            <span style="font-size:25px;">You are not allowed to access Online Appointments, please contact your administrator.</span>
          </div>
        </div>
      </template>
    </v-content>
  </v-app>
</template>

<script>
import '@mdi/font/css/materialdesignicons.css'
import 'vue-loading-overlay/dist/vue-loading.css';
import Vue from 'vue'
import Vuetify from 'vuetify/lib'
import SideBar from './components/SideBar'
import LeftMenu from './components/LeftMenu'
import HeaderBar from './components/HeaderBar'
import UsersSelection from './components/UsersSelection'
import Calendar from './components/Calendar'
import utils from './commons/utils'
import LCC from 'lightning-container'
import onBoardingModal from './components/onBoardingModal';
import Loading from 'vue-loading-overlay'


Vue.use(Vuetify)

export default {
  name: 'App',
  components: {
    Calendar,
    SideBar,
    LeftMenu,
    HeaderBar,
    UsersSelection,
    LCC,
    onBoardingModal,
    Loading

  },
  icons: {
    iconfont: 'mdi', 
  },
  data () {
    return { 
        fullPage: true,
        canCancel: false,
        useSlot: false,
        loader: 'dots',
        color: '#000000',
        bgColor: '#ffffff',
        timeout: 15000, //ms
      showOnBoarding: true,
      isHidden: false,
      calKey: 0,
      // searchQuery: 'Nathaniel',
      // nowTimeMS: new Date().getTime(),
      // dummyEvents: [
      //    {
      //       "definedCapacities":4,
      //       "DurationInMinutes":60,
      //       "freeCapacities":4,
      //       "gmtEndDateTime":this.nowTimeMS + 3600000,
      //       "gmtStartDateTime":this.nowTimeMS,
      //       "id":"00U0t000003BrfDEAS",
      //       "IsRecurrence":false,
      //       "localStoreEndDateTime":this.nowTimeMS + 3600000,
      //       "localStoreStartDateTime":this.nowTimeMS,
      //       "recordType":"Availability",
      //       "storeCode":"A06",
      //       "storeName":"LV PARIS CHAMPS ELYSEES",
      //       "timeZone":"Europe/Paris"
      //    },
      // ]
    }
  },
  methods:{
    hideOnboarding(){
      this.$modal.hide('onBoardingModal');
    },
    incrementCalKey(){
      //console.log('incrementCalKey')
      this.calKey += 1;
    },

    
  },
  computed:{
    isPilot(){
      return this.$store.state.isInPilotStores;
    },
    isVirtualEnabled(){
      return this.$store.state.isVirtualEnabled;
    },
    isLoading() {
      return this.$store.state.isFirstLoading;
    }
  },
  mounted: async function(){
    //utils.getStoreUsersAvailabilities();
    this.$store.dispatch("setFirstLoading", true);

    utils.getUserInfos();
    utils.getStoreInfos();
    utils.getProfiles();
    utils.getStoreHierarchy();
    utils.isPilot();
    utils.isVirtualEnabled();
    utils.getAuthorizedProfiles();
    utils.getUserPermissionSet();

    //StoreHierarchyModal.userStore(this.$store.state.storeInfos);
    

    // utils.getDateRangeAvailabilities(false, {start: moment().subtract(7, 'days').format('YYYY-MM-DD'), end: moment().add(7, 'days').format('YYYY-MM-DD')});
    // utils.getOnlineAndTeamAppointments('month');
   

    //this.$modal.show('onBoardingModal');      // a decommenter en cas de besoin - Naomi   

   
    this.$root.$on('rerenderCal', this.incrementCalKey);    
       

        
    

  },
  updated(){


    //open the modal store hierarchy for the first loading for every profile authorized --> doesn't open on mounted function
    if(this.$store.state.isFirstLoadingModal==true) 
    {
             this.$modal.show('StoreHierarchyModal');
      }

  


    //old version
    // if(Object.keys(this.$store.state.storeInfos).length === 0 && this.$store.state.storeInfos.constructor === Object){
    //   //no store infos
    //   this.$modal.show('StoreHierarchyModal')
    // }
    
  }
}
</script>

<style>

@import url('../static/img/assets/LVFont/font-faces.css');

  .application{
    font-family: 'Louis Vuitton';
  } 

html{
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}

/*  */
.v-content__wrap{
  display: flex;
}

.horizontal-wrapper {
    flex-grow: 1;
    height: auto;
}

.calendar-wrapper{
  height: 77vh;
  /* overflow-x: auto; */
}

button.floating-btn {
    margin: 74px 36px;
    height: 66px;
    width: 66px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12) !important;
    border-style: solid;
    border-width: 0.5px;
    border-image-source: linear-gradient(to bottom, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.06) 20%, rgba(255, 255, 255, 0));
    border-image-slice: 1;
    bottom: 0 !important;
}

.calendar-footer{
    display: inline-flex;
    backdrop-filter: blur(10px);
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.1);
    bottom: 0;
    width: 100%;
    position: fixed;
    align-items: center;
    justify-content: start;
    padding: 15px 0 15px 5px;
    z-index: 39;
    margin-left: 0;
    background: #fafafa;
}

.calendar-footer-left,
.calendar-footer-center,
.calendar-footer-right{
  font-size: 15px;
  line-height: 0.6;
  color: #2e3a49;
  cursor: pointer;
  vertical-align: sub;
  margin-left: 58px;
}

.calendar-footer-left::before{
  background-image: linear-gradient(45deg, #d3d3d3 16.67%, #fff0f0 16.67%, #fff0f0 50%, #d3d3d3 50%, #d3d3d3 66.67%, #fff0f0 66.67%, #fff0f0 100%);
  background-size: 4.24px 4.24px;
  width: 18px;
  height: 18px;
  border: solid 1px #d3d3d3;
  border-radius: 100%;
  content: '';
  display: inline-block;
  margin-right: 14px;
}

.calendar-footer-center, .calendar-footer-right {
    display: flex;
    align-items: center;
}

.calendar-footer-right-img, .calendar-footer-center-img{
  margin-right: 15px;
}


@media screen and (max-width: 1024px) {
  .modal-container {
    width: 434px !important;
    padding: 25px 25px 35px !important;
  }

  .calendar-footer-left, .calendar-footer-center, .calendar-footer-right {
    font-size: 14px;
    margin-left: 30px;
  }
}

@media screen and (max-height: 915px) {
  .calendar-wrapper{
        height: 77.5vh;
  }
}

@media screen and (max-height: 768px) {
  .calendar-wrapper{
        height: 73.5vh;
  }
}



/* TEMP LOADER */
.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #000;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #000 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* TEMP LOADER */


.confirm-button-alert-class{
        background-color: #000000 !important;
        color: #ffffff !important;
        font-size: 15px;
        min-width: 158px;
        min-height: 46px;
        outline: none;
        margin-top: 10px;
       border-radius:0px !important;
    }

  .cancel-button-alert-class{
      background-color: #eae8e4 !important;
      color: #000000 !important;
      font-size: 15px;
      min-width: 158px;
      min-height: 46px;
      outline: none;
      margin-top: 10px;
      border-radius:0px !important;
  }

</style>




