                  <div class="online-appointment-bottom-left">{{event.ownerName}}</div>
                  <div class="online-appointment-bottom-left-unassigned"><img class="assign-select-placeholder-img" src="@/assets/group-6.png" /><div class="assign-select-placeholder">Unassigned</div></div>
                  
