              <!-- Online Appointment more than 4 in day vue-->
               <div
                v-else-if="((event.recordType == 'Online_Appointment' || event.recordType == 'Appointments') && value.filter(item => item.recordType == 'Online_Appointment'  || item.recordType == 'Appointments' || item.recordType == 'Availability').length > 6)  && calendarType == 'day' 
               && value.filter(item => item.recordType == 'Online_Appointment'  || item.recordType == 'Appointments').findIndex(item => item.id == event.id) > (value.filter(item => item.recordType == 'Online_Appointment'  || item.recordType == 'Appointments').length - 6 - 1)" 
