              >
                <img v-if="(event.recordType == 'Online_Appointment' || event.recordType == 'Appointments') && (index == value.filter(item => item.recordType == 'Online_Appointment'  || item.recordType == 'Appointments' || item.recordType == 'Availability').length -1)" class="multiple-plus-btn" src="@/assets/base.png" />
                <div class="online-appointment-top">      
