              <div v-if="date == mouseDownDate" class="tempDiv" :key="event.recordType + Math.random()" :style="{ 
              top: (timeToY(mouseDownTime)<timeToY(mouseMoveTime)? timeToY(mouseDownTime):timeToY(mouseMoveTime)) + 'px', 
              bottom:  'calc(100% - ' + (timeToY(mouseDownTime)<timeToY(mouseMoveTime)? timeToY(mouseMoveTime):timeToY(mouseDownTime)) + 'px)', 
