      optionsHours(){
        let n = 24;
        let options = [];
        for (var i=0; i<n; i++) {
          if(i.toString().length == 1){
               i = '0' + i; 
          }
          options.push(i + ':00') //adds every round hours to the array
          options.push(i + ':30') //adds every half hours to the array
          
          for(var j=0; j<60; j++){
            if(j.toString().length == 1){
               j = '0' + j; 
            }
            options.push(i + ':' + j) //adds every minutes to the array
          }

