<template>
    <modal name="creationOSAModal"   
        class="creationOSAModal"    
        width="30%"
        height="auto"
        v-if="showModal" @close="showModal = false"
        @before-open="beforeOpen">
        <div class="creationOSAModalContent">
        <div class="modal-close" @click="closeModal()"></div>
            
            <div class="modal-assign-header">
                <!-- <h3 class="modal-assign-title">{{eventToShow.accountName}}</h3> -->
                <!-- <a :href="currentPath + '/' + model.accountId" target="_blank"><h3 class="modal-assign-title">{{propsReceived.accountName}}</h3></a> -->
                <!-- <div class="modal-title-icon"><img class="modal-title-icon-img" src="@/assets/noun-external-link-2584514.png" /></div> -->
            </div>

            <v-container grid-list-md text-xs-center class="inputs-container" style="padding-bottom: 60px;">
                <v-layout row wrap style="align-items: center;">
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <span>Client</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                            <v-autocomplete
                              v-model="model"
                              :disabled="isUpdating"
                              :items="entries"
                              :loading="isLoading"
                              :search-input.sync="search"
                                color="black"
                              filled
                             clearable
                               placeholder="Start typing to Search"
                               item-text="Name"
                              item-value="Id"
                              return-object
                              append-icon = "mdi-menu-down"
                              clear-icon = "mdi-close"
                              :filter="filterClients"
                            >
                              <template v-slot:item="data"  >
                                <div class="autocomplete-result-container">
                                    <div class="autocomplete-result-row">
                                        <span>{{data.item.Name}}</span> 
                                        <span>{{data.item.DREAMID__c ? data.item.DREAMID__c : ''}}</span>
                                    </div>
                                    <div class="autocomplete-result-row">
                                        <span>{{data.item.PersonEmail ? data.item.PersonEmail : ``}}</span>           
                                        <span>{{data.item.PersonMobilePhone ? data.item.PersonMobilePhone : ``}}</span>
                                    </div>
                                </div>
                              </template>

                              <template v-slot:selection="data"  >
                                  <div>
                                    <span>{{data.item.Name}} - </span> 
                                    <span>{{data.item.DREAMID__c ? data.item.DREAMID__c : ''}}</span>
                                  </div>
                              </template>
                            </v-autocomplete>
                        
                    </v-flex>
                </v-layout>
                <v-layout row wrap style="align-items: center;" v-if="model && !isClientEligible">
                  <v-flex >
                    <div class="warningLabel">
                            <div>The client has no email and\or no phone, please fill it in the client page <a :href="currentPath + '/' + (model ? model.Id : '')" target="_blank">{{model ? model.Name : ''}}</a> and refresh
                            <v-btn
                              
                              class="refreshClient-btn"
                              @click="refreshClient"
                              >
                              <v-icon size="20px">mdi-refresh</v-icon>
                            </v-btn>
                            </div>
                        </div> 
                     </v-flex>
                  </v-layout>
                <v-layout row wrap> <!-- row 2 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <span>Date</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                            <v-text-field
                                v-model="availability.start"
                                disabled
                                readonly
                                class="disabled-input"
                            ></v-text-field>
                    </v-flex>
                </v-layout>



                <v-layout row wrap> <!-- row 3 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <span>Hours</span>
                        </div> 
                    </v-flex>
                    <v-flex xs5 style="display: flex; padding: 4px 0;">
                        <v-flex xs5>
                             <v-text-field
                                v-model="availability.timeFrom"
                                disabled
                                readonly
                                class="disabled-input"
                            ></v-text-field>
                        </v-flex>
                      
                        <span class="toLabel-assign">to</span>
                      
                        <v-flex xs5>
                            <v-text-field
                                v-model="availability.timeTo"
                                disabled
                                readonly
                                class="disabled-input"
                            ></v-text-field>
                    </v-flex>
                    </v-flex>
                </v-layout>
                <v-layout row wrap v-if="isVirtualEnabled"> <!-- row 3 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <span>Appointment Type</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        <v-radio-group v-model="selectedRadio" class="radio-aptType">
                            <div class="radio-aptType-row" v-show="storeAptType.includes('inStore')">
                                <v-radio label="In store appointment" value="inStore" color="black" on-icon="mdi-radiobox-marked" off-icon="mdi-radiobox-blank"></v-radio>
                            </div>
                            <div class="radio-aptType-row" v-show="storeAptType.includes('distant') && hasAppointmentType">
                                <v-radio label="Virtual appointment" value="distant" color="black" on-icon="mdi-radiobox-marked" off-icon="mdi-radiobox-blank"> </v-radio>
                            </div>
                        </v-radio-group>
                    </v-flex>
                </v-layout>

            </v-container> 
            <button @click="closeModal()" class="eventTypeBtn delete-btn">
                Close
            </button>           
            <button @click="saveOSA()" class="eventTypeBtn" v-if="isCSCUser" :disabled="!isClientEligible">
                Save
            </button>
        </div>        
    </modal>
</template>

<script>
import moment from 'moment'
import LCC from 'lightning-container';
import Vue from 'vue'
import utils from '../commons/utils'

export default {
  name: 'creationOSAModal',
  components: {

  },
  props:['eventToShow'],
  data () {
    return {        
        showModal: true,
        timeFrom: null,
        timeTo: null,
        start: '',
        selectedRadio:'inStore',

        descriptionLimit: 60,
        entries: [],
        isLoading: false,
        model: null,
        search: null,
        accountId:null,
        currentStore:{},
        storeAPtType:'inStore;distant'
        
        
    }
  },
  methods:{
    beforeOpen(event){
      this.currentStore = this.$store.state.storeInfos;
      console.log(this.currentStore)
      let storeAPtType  =  this.currentStore.AppointmentType__c ? this.currentStore.AppointmentType__c.split(";") : 'inStore'; 
      this.selectedRadio = storeAPtType.length < 2 ? storeAPtType[0] : 'inStore';
    },
      closeModal(){
          this.$modal.hide('creationOSAModal');
          this.$emit('closeAvailability');
      },
      fullUserName(data){
          return data.FirstName + ' ' + data.LastName
      },
      saveOSA() {
        console.log(this.availability)
        this.$store.dispatch("setFirstLoading", true);
        utils.createOnlineAppointment(this.availability.id, this.model.Id, this.closeModal, this.selectedRadio)
      },
      clear () {
      this.model = null;
      },
      

          msToTime(ms){
        var d = new Date(ms);
        var f = d.getHours();
        if(f && f.toString().length == 1){
            f = '0' + f
        }
        return f + ':00';
      },
    msToDate(ms){
        return moment(ms).tz(this.storeInfos.Time_Zone_Id__c).format('YYYY-MM-DD');
    },
    getLocaleHour(datems){

        let localHour = null;
        if(datems){
            localHour = moment(datems).tz(this.storeInfos.Time_Zone_Id__c).format('HH');
        }
        return localHour
      },

    searchByTextClient(val, refreshClient = false){
      console.log(val)
        // Items have already been requested
        if (!val || val.length < 3) return
        // if (this.isLoading) return

        this.isLoading = true

        if (process.env.NODE_ENV === 'production') {
              LCC.callApex(
                    'OnlineAppointmentsController.searchClients',
                    val,
                    (result, event) => {
                      if (event.status) {
                        this.count = result.length;
                        this.entries = result;
                        this.isLoading = false
                        if(refreshClient && this.model.Id == this.entries[0].Id){
                          this.model = this.entries[0]
                        }
                      } else if (event.type === 'exception') {
                        this.isLoading = false
                          console.log(event.message + ' : ' + event.where)
                      } else {
                        this.isLoading = false
                          console.log('Unknown Error', event)
                      }
                    },
                    {escape: false}
                )
        } else {
          let result = [{Id:"001i000002EdlS4AAJ", Salutation:"MMe", FirstName:"Revaa", LastName:"ABDALAAHI", Name:"Revaa ABDALAAHI", DREAMID__c:85772245, PersonEmail:"nezhacherif@email.com", Can_Be_Contacted_By_Email__pc:true, Can_Be_Contacted_By_SMS__pc:false}]
            this.isLoading = false
            this.count = result.length;
            this.entries = result;
            console.log('not in production')
        }
      },

      refreshClient() {
        if(this.model )
          this.searchByTextClient(this.model.DREAMID__c || this.model.Name, true);
      },
      filterClients(item, queryText, itemText) {
          return true;
      }

  },
  
  computed:{

      storeAptType(){
          let store = this.$store.state.storeInfos
          return (store && store.AppointmentType__c ? store.AppointmentType__c : "inStore;distant")
      },

    availability(){
        if(this.eventToShow) {
            let eventObj = this.eventToShow;
            console.log(eventObj)
            eventObj.timeFrom=this.getLocaleHour( eventObj.gmtStartDateTime ? eventObj.gmtStartDateTime : eventObj.localStartDateTime     )+":00";
            eventObj.timeTo=this.getLocaleHour(eventObj.gmtEndDateTime)+":00";
            eventObj.start=this.msToDate(eventObj.gmtStartDateTime);
            return eventObj;
        }

        return {}
        
    },
    nowTime(){
      var d = new Date();
      return d;
    },
    userProfile(){
      var allProfiles = this.$store.state.profiles;
      var actualUser = this.$store.state.userInfos;
      var userProfile = allProfiles.length > 0 ? allProfiles.find(element => element.Id == actualUser.ProfileId) : ''
      return userProfile ? userProfile.Name : '';
    },   
    storeInfos(){
       return this.$store.state.storeInfos;
    },
    isUserActionAuthorized(){
        return utils.isUserActionAuthorized();
    },
    isCSCUser(){
          return utils.isCSCUser();
      },
    currentPath(){
        return window.location.origin;
    },

    isClientEligible () {
        return this.model && this.model.Id && this.model.PersonEmail && (this.model.PersonMobilePhone || this.model.PersonOtherPhone || this.model.PersonHomePhone)
    },
    isVirtualEnabled(){
      return this.$store.state.isVirtualEnabled;
    },
    hasAppointmentType(){
        var aptType = this.$store.state.storeInfos.AppointmentType__c;
        return aptType ? true : false
    }
}, 

watch: {
        search: utils.debounce(function (newVal) {
                console.log(newVal)
              this.searchByTextClient(newVal)
          },500)
}

}
</script>

