        margin-right: 12px;
         padding: 12px 45px;
    }

    .cancel-btn{
        background-color: #990000;
        color: #eae8e4;
        margin-right: 12px;
         padding: 12px 45px;
    }

    .inputsLabel{
        text-align: left;
        padding-top: 17px;
        padding-bottom: 17px;
        opacity: 0.5;
