<template>
  <modal name="MultipleEventModal"        
        width="30%"
        height="auto"
    >
    <div v-if="getEventsToShow" @mousedown="stopPropagation" @mouseup="stopPropagation">
      <div class="multiple-event-popup-layout" @click="clickOnLayout()"></div>
        <div class="multiple-event-popup" v-if="getEventsToShow[0]">
            <div class="multiple-event-popup-top">
            <div class="popup-top-left">
                <div class="popup-top-left-weekday">{{msToWeekDay(getEventsToShow[0].gmtStartDateTime)}}</div>
                <div class="popup-top-left-day">{{msToDate(getEventsToShow[0].gmtStartDateTime)}}</div>
            </div>
            <div class="popup-top-right">
                <div class="popup-top-right-hour">{{getLocaleHour(getEventsToShow[0].gmtStartDateTime) + ':00 - ' + getLocaleHour(getEventsToShow[0].gmtEndDateTime) + ':00'}}</div>
                <div class="popup-top-right-num">{{getEventsToShow.filter(item => item.recordType == 'Online_Appointment'  || item.recordType == 'Appointments').length }} Appointments</div>
                <div class="popup-top-right-num" v-if="getEventsToShow.filter(item => item.recordType == 'Availability').length !== 0">{{getFreeCapacities != 0 ? (getFreeCapacities > 1 ? getFreeCapacities + ' Available slots' : getFreeCapacities + ' Available slot' ) : '' }}</div>
                <div class="modal-close" @click="clickOnLayout()"></div>
            </div>
            </div>
            <div class="multiple-event-popup-bottom">
              <template v-for="eventA in getEventsToShow">
                <template>
                  <div :class=" 'multiple-list-event ' + onlineAppointmentClassTag(eventA) + ' ' + assignedClassTag(eventA) + ' ' + availabilityClassTag(eventA)" @click="open(eventA)">
                    <div class="multiple-list-event-name">{{eventA.accountName}}</div>
                    <div class="multiple-list-event-sa"><img class="multiple-owner-name-img" :src="getPictureUrl(eventA)" /> {{eventA.ownerName}}</div>
                    <div class="multiple-list-event-unassigned"><img class="assign-select-placeholder-img" src="@/assets/group-6.png" /><div class="assign-select-placeholder">Unassigned</div></div>
                    <div class="multiple-list-event-status" style="width: 30px;"></div>
                    <div class="multiple-list-event-availability">{{eventA.freeCapacities > 1 ? eventA.freeCapacities + ' Available slots' : eventA.freeCapacities + ' Available slot' }}</div>
                  </div>
                </template>
              </template>
            </div>
        </div>
      </div> 
      <div v-else>
        An error occured!
      </div>   
    </modal>
</template>

<script>
import LCC from 'lightning-container';
import Vue from 'vue'
import moment from 'moment'
import 'moment-timezone';

export default {
  name: 'MultipleEventModal',
  components: {
  },
  props:['dateRange'],
  data () {
    
    return {
      eventToAssign: {},
      availToEdit: {},
      hidePopup: true,
    }
  },
  methods:{
    clickOnLayout(){
      this.$root.$emit('rerenderCal');
      this.hidePopup = false
      this.$emit('closeMultipleEventPopup');
    },

    open (event) {
      console.group('open',event);

      if(event.recordType == 'Availability'){
        this.$emit('showAvailToEdit', event);
      }
      else{
        this.$emit('showAssignModal', event);
      }
    },
    stopPropagation(e) {
      e.stopPropagation();
    },
    msToDate(ms){
      return moment(ms).tz(this.storeInfos.Time_Zone_Id__c).format('YYYY-MM-DD');
    },
    msToDay(ms){
      var d = new Date(ms);
      return d.getDate();
    },
    msToWeekDay(ms){
      var d = new Date(ms);
      let realDate = new Date(ms).toLocaleString('en-us', {weekday: 'short'});
      return realDate;
    },
    msToDateObj(ms){
      var d = new Date(ms);
      return d;
    },
    msToTime(ms){
      var d = new Date(ms);
      return d.getHours() + ':00';
    },
    onlineAppointmentClassTag(event){
      if (event.recordType == "Online_Appointment"){
        return "online-appointment-tag"
      }
      if (event.recordType == "Appointments"){
        return "appointment-tag"
      }
    },
    assignedClassTag(event){
      if (event.status == "Assigned"){
        return "assigned-tag"
      }
      if(event.status == "Not Assigned"){
        return "unassigned-tag"
      }
      if(event.status == "Cancelled"){
        return "cancelled-tag"
      }
    },
    availabilityClassTag(event){
      if (event.recordType == "Availability"){
        return "availability-tag"
      }
      else{
        return "not-availability-tag"
      }
    },
    getPictureUrl(event){
      let filterUser = this.availableTeamUsers.filter(item => item.Id == event.ownerId)
      let userUrl = '';
      if(filterUser[0])
        userUrl = filterUser[0].FullPhotoUrl
      return userUrl
    },
    getLocaleHour(datems){
      //console.log(datems)
      let localHour;
      if(datems && this.storeTZ){
        localHour = moment(datems).tz(this.storeTZ).format('HH');
      }
      //console.log(localHour)
      return localHour
    },   

    getStatusPriority(event){
        if(event.recordType == 'Online_Appointment' && event.status == 'Not Assigned'){
          return 4;
        } else if(event.recordType == 'Online_Appointment' && event.status == 'Assigned'){
          return 3;
        } else if(event.recordType == 'Appointments'){
          return 2;
        } else {
          return 1;
        }
      },
  },
  
  computed:{
      propsReceived(){
        return JSON.parse(this.dateRange)
      },
      storeTZ(){
        return this.$store.state.storeInfos.Time_Zone_Id__c;
      },
      storeInfos(){
        return this.$store.state.storeInfos;
      },
      getEventsToShow(){
        // console.log(this.propsReceived.mouseDownTime)
        //let eventsArray = this.$store.state.events;

        let eventsMap = this.$store.state.eventsMap;

        console.log("blou", eventsMap);
        // let mdt = this.$store.state.mouseDownTime;
        let mdt = ("0" + this.$store.state.mouseDownTime).slice(-5);
        let mdd = this.$store.state.mouseDownDate;

        if(eventsMap && mdd && eventsMap[mdd] &&  mdt ){

          let invertedArray = []
          let arrayE = eventsMap[mdd][mdt] ? eventsMap[mdd][mdt] : eventsMap[mdd][mdt.slice(-4)]

          if(arrayE && arrayE.length >0){
            for(let i=0; i< arrayE.length; i++){
              //check if event is an availability with 0 free capacity -> if yes, do not add it
              if((arrayE[(arrayE.length- 1) - i].recordType == 'Availability' && arrayE[(arrayE.length- 1) - i].freeCapacities != 0) || (arrayE[(arrayE.length- 1) - i].recordType != 'Availability')){
                invertedArray.push(arrayE[(arrayE.length- 1) - i])
              }
            }
          }
          return invertedArray.filter(event => event.status != "Cancelled" )

          // return eventsMap[mdd][mdt] ? eventsMap[mdd][mdt] : eventsMap[mdd][mdt.slice(-4)]
        } else {
          return null
        }
      },
      availableTeamUsers(){
          return this.$store.state.availableTeamUsers;
      },
      events(){
        return this.$store.state.events;
      },
      getFreeCapacities(){
        return this.getEventsToShow.filter(item => item.recordType == 'Availability')[0].freeCapacities;
      }
  }
}
</script>

