<template>
    <modal name="StoreHierarchyModal"        
        width="30%"
        height="auto"
    >
        <transition name="modal">
            <div class="storeHierarchy-modal-mask">
                <div class="storeHierarchy-modal-wrapper">
                    <div class="storeHierarchy-modal-container">

                        <div id="modalTitle" class="modal__title">
                            <p >{{title}}</p>
                            <button @click="closeModal()" class="close-btn">
                              <img src="../assets/close.png" />
                            </button>
                        </div>

                        <div id="multiselectContainer">
                          <div class="multiselect-label">Management Zone Level</div>
                          <v-select 
                            :items="Object.keys(storeHierarchy).sort()"
                            
                            class="storeHierarchyInput"
                            height="55px"
                            v-model="zones"
                            @change="addZone">
                            <v-icon slot="append" class="iconMarginRight">mdi-menu-down</v-icon>
                          </v-select>

                          <div class="multiselect-label">Country / Region</div>
                          <v-select 
                            :disabled="zones.length == 0" 
                            :items="Object.keys(countryOptions).sort()"
                            
                            class="storeHierarchyInput"
                            height="55px"
                            v-model="countries"
                            @change="addCountry">
                            <v-icon slot="append" class="iconMarginRight">mdi-menu-down</v-icon>
                          </v-select>

                          <div class="multiselect-label">Store</div>
                          <v-select 
                            :disabled="countries.length == 0" 
                            :items="storeOptions"
                            
                            item-text="Name"
                            class="storeHierarchyInput"
                            height="55px"
                            @change="storeSelected"
                            v-model="stores">
                            <v-icon slot="append" class="iconMarginRight">mdi-menu-down</v-icon>
                          </v-select>
                        </div>

                        <div class="storeHierarchy-modal-footer">
                          <button @click="closeModal()" class="eventTypeBtn cancel-btn">
                              Cancel 
                          </button>
                          <button @click="confirmStoreHierarchy()" class="eventTypeBtn">
                               <div v-if="!showLoader"> Confirm </div>
                            <div class="lds-spinner" v-if="showLoader"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
                          </button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

    </modal>
</template>

<script>
import utils from '../commons/utils'
export default {
  name: 'StoreHierarchyModal',
  components: {
    
  },
  data () {
    return {
        showLoader: false,
        title: "Select your store hierarchy",
        zones: [],
        countries: [],
        stores: [],
        disableBtn: true,
        validated: false,
        selectedStore:{},
    }
  },
  mounted: async function(){


    //when opening the modal store hierarchy, if there is already a default store in the user, we need to fill the v-select with the value of the default store

     if(Object.keys(this.$store.state.storeInfos).length>0)
      {

    

          this.zones=this.$store.state.storeInfos.MANAGEMENT_ZONE_LEVEL_TECH__c;
          this.countries=this.$store.state.storeInfos.StoreCountry__c;
          this.stores=this.$store.state.storeInfos.Name;
          this.storeSelected(this.$store.state.storeInfos.Name);

        }
         else
        {


       
          this.zones=this.$store.state.userInfos.MANAGEMENT_ZONE_LEVEL__c;
          if((this.$store.state.userInfos.MANAGEMENT_ZONE_LEVEL__c=='EMEA' || this.$store.state.userInfos.MANAGEMENT_ZONE_LEVEL__c=='AMERICAS')&& (this.countries=this.$store.state.userInfos.MGMT_ZONE_SUB_LEVEL3__c!=null))
          {
            this.countries=this.$store.state.userInfos.MGMT_ZONE_SUB_LEVEL3__c;

          }
          else{
            if(this.countries=this.$store.state.userInfos.MGMT_ZONE_SUB_LEVEL1__c!=null)
            {
          this.countries=this.$store.state.userInfos.MGMT_ZONE_SUB_LEVEL1__c;

            }


          }
        }

     
  },
  updated(){

    let currentStore = this.$store.state.storeInfos;
    
  },
  methods:{
      closeModal(){
        this.$modal.hide('StoreHierarchyModal');
      },
      confirmStoreHierarchy(){
        if(Object.keys(this.selectedStore).length > 0){
          this.$store.dispatch('setStoreInfos',  this.selectedStore); 
          console.log(this.$store)
           this.$store.dispatch('emptyEvents');
           this.$store.dispatch("setFirstLoadingModal", false); //indicate that the popup store hierarchy was opened on first loading
           console.log('this.$store.state.isFirstLoadingModal confirmStoreHierarchy:')
           console.log(this.$store.state.isFirstLoadingModal)
           utils.fetchAllDataByStore()

           this.$modal.hide('StoreHierarchyModal');
           this.$root.$emit('rerenderCal');
        } else {
          alert('Please select a store')
        }
      },
      beforeClose (event) {
        if (!this.isComplete) {
          event.stop()
        }
      },
      removeZone(z, id){
        this.countries = this.countries.filter(item => this.storeHierarchy[z][item] == undefined)
        this.stores = this.stores.filter(item => item.store_zone != z)
      },
      removeCountry(c, id){
        this.stores = this.stores.filter(item => item.country != c)
      },
      addZone(z, id){
        if(this.zones.length >= 1){
          console.log('in addzone')
          console.log(this.zones)
          this.countries = []
          this.stores = []
          this.selectedStore = {}
        }
      },
      addCountry(c, id){
        if(this.countries.length >= 1){
          this.stores = []
          this.selectedStore = {}
        }
      },
      storeSelected(storeName){
        this.selectedStore = this.storeHierarchy[this.zones][this.countries].find(element => element.Name == storeName);
      },
     
  },
  computed:{
    storeHierarchy() {      
      console.log("this.$store.state.storeHierarchy")
      console.log(this.$store.state.storeHierarchy)
      return this.$store.state.storeHierarchy;
    },
    isComplete () {
      return this.zones && this.zones.length > 0 && this.countries && this.countries.length > 0 &&  this.stores && this.stores.length > 0;
    },
    countryOptions(){
      if (this.zones.length == 0){
        return {}
      }
      return this.storeHierarchy[this.zones];
    },
    storeOptions(){
      if (Object.keys(this.countryOptions).length == 0){
        return []
      }

      // let a = this.storeHierarchy[this.zones][Object.keys(this.countryOptions)[0]]
      // let b = this.storeHierarchy
      // let c = this.zones
      // let d = Object.keys(this.countryOptions)
      if(this.storeHierarchy[this.zones] && this.storeHierarchy[this.zones][this.countries]){
        var toReturn = this.storeHierarchy[this.zones][this.countries].sort((a, b) => a.Name.localeCompare(b.Name));

        for(var i=0; i < toReturn.length; i++) {
          toReturn[i].Name = toReturn[i].Name.replace('&#39;', '\'');
        }

        return toReturn;
      }

      return [];
        
      
    }
  },
     
}
</script>
<style>

.storeHierarchy-modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .1);
  display: table;
  transition: opacity .3s ease;
}

.storeHierarchy-modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.storeHierarchy-modal-container {
  width: 479px;
  margin: 0px auto;
  padding: 34px 79px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  text-align: center;
  position: relative;
}

.storeHierarchy-modal-body {
  font-size: 17px;
  font-weight: 300;
  line-height: 1.18;
  color: #000000;
  margin-bottom: 32px
}

.storeHierarchy-modal-footer {
    /* display: flex;
    flex-wrap: nowrap; */
    display: flex;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
/* .v-input__icon{
    margin-right: 10px !important;
  } */
</style>
<style scoped>
  .eventTypeBtn{
    max-width: 130px;
    padding: 12px 30px;
  }
  .cancel-btn{
    background-color: #eae8e4;
    color: #000;
  }
  .multiselect-label{
    text-align: left;
  }
  .modal__title{
    display: flex;
    justify-content: space-between;
  }
  .modal__title > p{
    font-size: 22px;
    font-weight: bold;
  }
  .storeHierarchyInput{
    padding: 0;
  }
  .close-btn{
    height: 35px;
    margin-top: 3px;
    position: absolute;
    top: 5px;
    right: 10px;
  }
  .iconMarginRight{
    margin-right: 10px;
  }
</style>


