<template>
    <modal name="createEditSlotModal"        
        class="createEditSlotModal"
        width="30%"
        height="auto"
        @before-open="beforeOpen"
        @closed="closeModal"
    >
        <div class="createEditSlotModalContent">
        <div class="modal-close" @click="closeModal()"></div>
            
            <h3 class="modal-title">{{this.slotToEdit ? (this.isUserActionAuthorized ? "Edit Slot" : "View Details") :  "Create Slots"}}</h3>

            <v-container grid-list-md text-xs-center class="inputs-container">
                <v-layout row wrap> <!-- row 1 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <!-- <v-icon>event</v-icon> -->
                            <span>Date</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        <v-menu
                            :disabled="!isUserActionAuthorized"
                            v-model="dateMenu"
                            :close-on-content-click="false"
                            :nudge-right="-10"
                            transition="scale-transition"
                            offset-y
                            full-width
                            min-width="290px"
                        >
                            <template v-slot:activator="{ on }">
                                
                            <v-text-field
                                :disabled="!isUserActionAuthorized"
                                v-model="start" 
                                readonly
                                v-on="on"
                                append-icon="mdi-menu-down"
                            ></v-text-field>
                            </template>
                            <v-date-picker v-model="start" @input="dateMenu = false" color="black" prev-icon="mdi-chevron-left" next-icon="mdi-chevron-right"></v-date-picker>
                            
                        </v-menu>
                        
                    </v-flex>
                </v-layout>

                <v-layout row wrap>  
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <span>Hours</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9 style="display: flex; padding: 4px 0;">
                        <v-flex xs5>
                            <v-select
                            :disabled="!isUserActionAuthorized"
                            :items="optionsHours"
                            :menu-props="{ bottom: true, offsetY: true }"
                            v-model="timeFrom"
                            filled
                            append-icon="mdi-menu-down"
                            ></v-select>
                        </v-flex>
                        <v-spacer></v-spacer>
                        <span class="toLabel">to</span>
                        <v-spacer></v-spacer>
                        <v-flex xs5>
                            <v-select
                            :disabled="!isUserActionAuthorized"
                            :items="optionsHours"
                            v-model="timeTo"
                            :menu-props="{ bottom: true, offsetY: true }"
                            filled
                            append-icon="mdi-menu-down"
                            ></v-select>
                    </v-flex>
                    </v-flex>
                </v-layout>

                <v-layout row wrap> <!-- row 3 -->
                    <v-flex xs3>
                        <div class="inputsLabel">
                            <!-- <v-icon>people</v-icon> -->
                            <span>Capacity</span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        
                                <v-select
                                :disabled="!isUserActionAuthorized"
                                :items="capacityOptions"
                                :menu-props="{ bottom: true, offsetY: true }"
                                v-model="chosenCapacity"
                                filled
                                append-icon="mdi-menu-down"
                                ></v-select>
                       
                    </v-flex>
                </v-layout>

                <v-layout row wrap v-if="!showRecurrenceMenu"> <!-- row 3 -->
                        <div class="inputsLabel" >
                            <!-- <v-icon>people</v-icon> -->
                            <span>You can only create recurrence for slots of 5 hours maximum.</span>
                        </div> 
                </v-layout>
                <v-layout row wrap v-if="showRecurrenceMenu"> 
                    
                    <v-flex xs3>
                        <div class="inputsLabel">
                    
                            <span>Recurrence</span>
                        </div> 
                    </v-flex>
                    <div class="lds-spinner" v-if="showRecurrenceLoader ">
                        <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
                    </div>
                    <v-flex xs9 v-if="!showRecurrenceLoader ">
                        <v-layout row wrap style="flex-wrap: nowrap;">
                            <v-flex xs5 class="Reccurence-select">
                                <v-select
                                :disabled="!isUserActionAuthorized"
                                v-model="SelectedRecurrenceType"
                                :items="RecurrenceTypes"
                                item-text = "key"
                                item-value = "value"
                                filled
                                :menu-props="{ bottom: true, offsetY: true }"
                                append-icon="mdi-menu-down"
                                ></v-select>
                            </v-flex>
                        
                            <template  v-if="SelectedRecurrenceType == 'RecursWeekly'">
                                <button :disabled="!isUserActionAuthorized" v-for="{key, value} in days" :key="key" :class="(selectedWeeklyDays.indexOf(value) != -1 ? 'selectedDay weekDayBtn' : 'weekDayBtn')" @click="dayClicked(value)">{{key}}</button>
                            </template>
                        </v-layout>
                    </v-flex>
                </v-layout>

                <v-layout row wrap style="max-height: 135px;" v-if="showRecurrenceMenu && !showRecurrenceLoader "> 
                    <v-flex xs3>
                        <div class="inputsLabel">
                  
                            <span></span>
                        </div> 
                    </v-flex>
                    <v-flex xs9>
                        
                        <v-radio-group v-model="selectedRadio" class="radio-ends" v-show="SelectedRecurrenceType != 'NO'" :disabled="!isUserActionAuthorized">
                            <div class="radio-ends-row">
                                <v-radio label="Never" value="never" color="black" on-icon="mdi-radiobox-marked" off-icon="mdi-radiobox-blank"></v-radio>
                            </div>
                            <div class="radio-ends-row" >
                                <v-radio label="On" value="on" color="black" on-icon="mdi-radiobox-marked" off-icon="mdi-radiobox-blank"> </v-radio>
                                <v-flex xs6>
                                    <v-menu
                                    v-model="showEndDatePicker"
                                    :close-on-content-click="true"
                                    :nudge-right="-20"
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                        <template v-slot:activator="{ on }">
                                            <v-text-field
                                                v-model="end"
                                                readonly
                                                v-on="on"
                                            ></v-text-field>
                                        </template>
                                        <v-date-picker 
                                            v-model="end" 
                                            :allowed-dates="recurrenceAllowedDates"
                                            show-current="false"
                                            color="black" 
                                            prev-icon="mdi-chevron-left"
                                            next-icon="mdi-chevron-right">
                                        </v-date-picker>
                                    </v-menu>
                                </v-flex>
                            </div>
                    </v-radio-group>
                       
                    </v-flex>
                </v-layout>

            </v-container>
            <div class="footer-btn">
                <button @click="openCreationOSAModal()" class="eventTypeBtn" v-if="isCSCUser && slotToEdit && slotToEdit.freeCapacities > 0 && isFutureEvent ">
                    Create Appointment
                </button>
                <button @click="openDeleteModal()" class="eventTypeBtn delete-btn" v-if="isUserActionAuthorized && slotToEdit">
                    Delete
                </button>
                <button @click="saveAvailability(eventType)" class="eventTypeBtn" v-if="isUserActionAuthorized" :disabled="showLoader == 'loading'">
                    <div v-if="!showLoader"> Save </div>
                    <div class="lds-spinner" v-if="showLoader == 'loading'">
                        <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
                    </div>
                    <div v-if="(!slotToEdit || !slotToEdit.RecurrenceActivityId) && showLoader == 'error'">
                        <span>An error occurred</span>
                    </div>
                </button>
            </div>
        </div> 
        <confirmDeleteModal v-on:confirmDeleteAvailability="deleteAvailability" />    
        <confirmRecurringModeModal v-on:confirmRecurringModeSelect="executeRecurringAction" :title="recurringTitleAction" />    
        <creationOSAModal :eventToShow="slotToEdit"  v-on:closeAvailability="closeModal"/>        
    </modal>
</template>

<script>
import moment from 'moment';
import 'moment-timezone';
import LCC from 'lightning-container';
import utils from '../commons/utils';
import confirmDeleteModal from './ConfirmDeleteModal'
import creationOSAModal from './CreationOSAModal'
import confirmRecurringModeModal from './ConfirmRecurringModeModal'

export default {
  name: 'createEditSlotModal',
      components:{
        confirmDeleteModal,
        creationOSAModal,
        confirmRecurringModeModal
        },
    props:['slotToEdit'],
  data () {
    return {
      recurringAction:null,
      recurringTitleAction:null,  
      recurrenceRefStartDateTime: null,
      showLoader: null,
      type: 'day',
      end: null,
      start: null,
      lowestTime: '',
      duration: '',
      event: {},
      eventType: 'Availability',      
      dateMenu: false,
      dateMenuEnds: null,
      timePickerMenuFrom: false,
      timePickerMenuTo: false,
      timeFrom: null,
      timeTo: null,
      modal: false,
      //capacityOptions: [...Array(20).keys()],
      chosenCapacity: 1,
      SelectedRecurrenceType:'NO',
      RecurrenceTypes:[
        {key:"None", value: "NO"},
        {key:"Daily", value: "RecursDaily"},
        // {key:"Every Weekday",value: "RecursEveryWeekday"},
        // {key:"Monthly",value: "RecursMonthly"},
        // {"Daily",value: "RecursMonthlyNth"},
        {key:"Weekly",value: "RecursWeekly"},
        // {key:"Yearly",value: "RecursYearly"},
        // {"Daily" : "RecursYearlyNth"}
      ],
      selectedDayForRecurrence: [],
      showEndDatePicker: false,
      radios: 'never',
      days:[
        {key:"S", value: 1},
        {key:"M", value: 2},
        {key:"T",value: 4},
        {key:"W",value: 8},
        {key:"TH",value: 16},
        {key:"F",value: 32},
        {key:"S",value: 64},
      ],
      isSelectDay: undefined,
      slotsToUpsert: [],
      showRecurrenceLoader: false
    }
  },
  methods:{
      beforeOpen(e) {
          this.SelectedRecurrenceType = 'NO'
            this.recurrenceRefStartDateTime = null
            this.end = null
            this.selectedWeeklyDays = []
        if(e.params && e.params.lowestTime){
            this.lowestTime = e.params.lowestTime;
            this.duration = e.params.duration; 
            this.event = e.params.event; 

            this.timeFrom = this.lowestTime;
            this.timeTo = moment.utc(this.timeFrom,'HH:mm').add(this.duration / 3600000,'hour').format('HH:mm')  

            let mddStore = this.$store.state.mouseDownDate;
            if(mddStore == "NaN-aN-aN"){
                mddStore = new Date().toISOString().substr(0, 10)
            }
            this.start = mddStore
        } else if (e.params) {
            if(e.params.event.RecurrenceActivityId) {
                this.getRecurrenceRefSlot(e.params.event.RecurrenceActivityId)
            }

            this.timeFrom=this.getLocaleHour( e.params.event.gmtStartDateTime ? e.params.event.gmtStartDateTime : e.params.event.localStartDateTime     )+":00";
            this.timeTo=this.getLocaleHour(e.params.event.gmtEndDateTime)+":00";
            this.start=this.msToDate(e.params.event.gmtStartDateTime);
            this.chosenCapacity = e.params.event.definedCapacities;
            
        }
      },
      saveAvailability(eventType){
        if(this.timeFrom && this.timeTo){
            if(this.timeFrom > this.timeTo){
                alert("End hour cannot be before start. Please verify the hours fields");
                return 
            } else {
                let durationH = moment.utc(moment(this.timeTo,"HH:mm").diff(moment(this.timeFrom,"HH:mm"))).format("H");
                if(durationH > 1 && this.slotToEdit && this.slotToEdit.id) {
                    this.$alert(
                        `When update a slot, the duration has to been 1 hour.`,
                        '',
                        'warning');
                }
          
                for(var i=0; i<durationH; i++){
                    let eventStartTime = moment(this.timeFrom, "HH:mm").add(i, 'hour').format('HH:mm')
                    let eventEndTimeMS = moment(this.timeFrom, "HH:mm").add(i+1, 'hour').valueOf()
                    // console.log(new Date(moment.tz(this.start + ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).format()))

                    var RecurrenceEndDateOnly = null;
                    var RecurrenceInterval = null;
                    var RecurrenceDayofWeekMask = null; 
                    var IsRecurrence = false;
                    var RecurrenceDayOfMonth = null;
                    var RecurrenceMonthOfYear = null;

                    // no recurrence ?
                    if(this.SelectedRecurrenceType == "NO"){
                        RecurrenceEndDateOnly = moment(this.start).valueOf();
                    } else {
                        IsRecurrence = true;
                    }

                    //calculate end of recurrence
                    if(IsRecurrence && this.selectedRadio == "never"){
                        // RecurrenceEndDateOnly = (this.start ? moment(this.start).add(this.getMaxEndDayRecurrencesAllowed(), 'days') : moment().add(this.getMaxEndDayRecurrencesAllowed(), 'days')).valueOf();
                        RecurrenceEndDateOnly = this.start ? new Date(moment.tz(this.start+ ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).add(this.getMaxEndDayRecurrencesAllowed(), 'days').format()).getTime() :
                             new Date(moment.tz(moment(), this.storeInfos.Time_Zone_Id__c).add(this.getMaxEndDayRecurrencesAllowed(), 'days').format()).getTime();
                        IsRecurrence = true;
                    }
                    else if(IsRecurrence && this.selectedRadio == "on"){
                        if(this.end){
                            // RecurrenceEndDateOnly = moment.tz(this.end, this.storeInfos.Time_Zone_Id__c).valueOf(); 
                            RecurrenceEndDateOnly = new Date(moment.tz(this.end + ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).format()).getTime()
                        } else {
                            //RecurrenceEndDateOnly = (this.start ? moment.tz(this.start, this.storeInfos.Time_Zone_Id__c).add(this.getMaxEndDayRecurrencesAllowed(), 'days') : moment().add(this.getMaxEndDayRecurrencesAllowed(), 'days')).valueOf();

                            RecurrenceEndDateOnly = this.start ? new Date(moment.tz(this.start+ ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).add(this.getMaxEndDayRecurrencesAllowed(), 'days').format()).getTime() :
                             new Date(moment.tz(moment(), this.storeInfos.Time_Zone_Id__c).add(this.getMaxEndDayRecurrencesAllowed(), 'days').format()).getTime();
                        }
                        
                        IsRecurrence = true;
                    }
                    
                    if(this.SelectedRecurrenceType == "RecursDaily" ){
                        RecurrenceInterval = 1;
                        IsRecurrence = true;
                    }

                    if(this.SelectedRecurrenceType == "RecursWeekly"){
                        RecurrenceInterval = 1;
                        IsRecurrence = true;
                        var startDay = moment(this.start).day();
                        RecurrenceDayofWeekMask = this.days[startDay].value;
                        if(this.selectedWeeklyDays.length > 0){
                            RecurrenceDayofWeekMask = this.selectedWeeklyDays.reduce((a, b) => a + b, 0); //sum of the selectedDayForRecurrence array's values 
                        }
                    }


                    this.slotsToUpsert.push(
                        {
                            // title: eventType,
                            // date: this.start,  
                            // time: eventStartTime,
                            DurationInMinutes: 60,
                            capacities:this.chosenCapacity,
                            id: this.slotToEdit ? this.slotToEdit.id : null,
                            storeCode:this.storeInfos.RetailStoreId__c,

                            IsRecurrence: IsRecurrence,
                            RecurrenceStartDateTime: new Date(moment.tz(this.start + ' ' + eventStartTime, this.storeInfos.Time_Zone_Id__c).format()).getTime(),
                            RecurrenceEndDateOnly: RecurrenceEndDateOnly,
                            RecurrenceType: this.SelectedRecurrenceType != 'NO' ? this.SelectedRecurrenceType : null,
                            RecurrenceInterval: RecurrenceInterval,
                            RecurrenceDayofWeekMask: RecurrenceDayofWeekMask,
                            RecurrenceDayOfMonth: RecurrenceDayOfMonth,
                            RecurrenceActivityId: this.slotToEdit ? this.slotToEdit.RecurrenceActivityId : null
                            //RecurrenceTimeZoneSidKey: this.storeInfos.Time_Zone_Id__c,
                        }
                    );

                }

                //create avaiklability in SF
                if(!this.slotToEdit || !this.slotToEdit.id) {
                    this.createAvailabilityInSF();
                } else {
                    this.upsertAvailability()
                }
                
            }
        } else{
            alert("Please fill Hours fields to save your availability");
        }
      },
      closeModal(){
          this.showLoader = null;
          this.$modal.hide('createEditSlotModal');
      },
      openCreationOSAModal(){
          this.$modal.show('creationOSAModal');
      },
      openDeleteModal(){

          if(this.slotToEdit.RecurrenceActivityId) {
                this.deleteAvailability()
            } else {
                 this.$modal.show('confirmDeleteModal');
            }
      },
      openRecurringModeModal(){
          this.$modal.show('confirmRecurringModeModal');
      },
      makeSelectDay: function() {
          this.isSelectDay = !this.isSelectDay;
      },
      getRecurrenceRefSlot(recurrenceActivityId){
        if (process.env.NODE_ENV === 'production') {
            this.showLoader = 'loading'
            LCC.callApex(
                'OnlineAppointmentsController.getRecurrenceRefSlot',
                recurrenceActivityId,
                (result, event) => {
                if (event.status) {
                    //Populate Recurrence fields
                    this.SelectedRecurrenceType = result.RecurrenceType
                    this.recurrenceRefStartDateTime = result.gmtStartDateTime
                    this.end = this.msToDate(result.RecurrenceEndDateOnly)
                    this.selectedWeeklyDays = this.setRecurrenceDayofWeekMask(result.RecurrenceDayOfWeekMask)
                    console.log(this.selectedWeeklyDays);
                } else if (event.type === 'exception') {
                    console.log(event.message + ' : ' + event.where)
                    this.showLoader = 'error'
                } else {
                    console.log('Unknown Error', event)
                }
                this.showLoader = null
                },
                {escape: false}
            )
            } else {
            console.log('not in production')
             
            }
        },
        setRecurrenceDayofWeekMask(RecurrenceDayofWeekMask){
            var selected = []
            // let values = [64, 32, 16, 8, 4, 2, 1]
            let values = this.days.map((item) => item.value).sort((a,b)=>b-a) 
            values.map((item) => {
                if(RecurrenceDayofWeekMask - item >=0){
                    RecurrenceDayofWeekMask -= item
                    selected.push(item)
                }
            })
            return selected
        },
      createAvailabilityInSF(){
        if (process.env.NODE_ENV === 'production') {
            //let params = JSON.stringify(this.$store.state.events);
            this.showLoader = 'loading'
            LCC.callApex(
                'OnlineAppointmentsController.createAvailabilities',
                JSON.stringify(this.slotsToUpsert),
                (result, event) => {
                if (event.status) {
                    this.handleLCCQueryResponse(result)
                    this.showLoader = null
                    this.$root.$emit('rerenderCal');
                    this.$modal.hide('createEditSlotModal');
                } else if (event.type === 'exception') {
                    console.log(event.message + ' : ' + event.where)
                    this.showLoader = 'error'
                } else {
                    console.log('Unknown Error', event)
                }
                },
                {escape: false}
            )
            this.slotsToUpsert = [];

            } else {
            console.log('not in production')
             this.$modal.hide('createEditSlotModal');
             this.showLoader = 'error';
             this.selectedWeeklyDays = [];
             this.$store.dispatch('addEvents', this.slotsToUpsert);
             this.slotsToUpsert = [];
            }
            console.log(' ');
        },
        handleLCCQueryResponse(response) {

            // if (response.existingSlots && response.existingSlots.length > 0) {
            //     this.$alert(
            //        `${response.createdAvailabilities && response.createdAvailabilities.length > 0 ?`Slot${result.deletedAvailabilities.length > 1 ?"s were" : "was"} created but ` : `` } your store already has slots existing at the following times:${response.existingSlots.map((rec) => moment.tz(rec, this.storeInfos.Time_Zone_Id__c).format('LLL')).join(', ')}`,
            //         '',
            //         'warning');
            // }
        },
      timePickerAllowedMinutes: m => m % 60 === 0,
      dayClicked(day){
        if(this.selectedWeeklyDays.indexOf(day) == -1){
          this.selectedDayForRecurrence.push(day)
        } else {
          this.selectedDayForRecurrence.splice(this.selectedDayForRecurrence.indexOf(day), 1)
        }
      },
    //allowedDates: val => parseInt(val.split('-')[2], 10) % 2 === 0,
    recurrenceAllowedDates(val){
        return moment(val) > moment() && moment(val) < (this.start ? moment(this.start).add(this.getMaxEndDayRecurrencesAllowed(), 'days') : moment().add(this.getMaxEndDayRecurrencesAllowed(), 'days'));
    },

    getMaxEndDayRecurrencesAllowed(){
          if(this.SelectedRecurrenceType == 'RecursDaily'){
              return 99;
          } else if (this.SelectedRecurrenceType == 'RecursWeekly'){
              let nbRecurr = this.selectedWeeklyDays && this.selectedWeeklyDays.length > 0 ? this.selectedWeeklyDays.length : 1;
              return Math.floor(-nbRecurr + 53*7/nbRecurr);
          }
      },
    deleteAvailability(){
        
        if (process.env.NODE_ENV === 'production') {
            //let params = JSON.stringify(this.$store.state.events);
             if(this.slotToEdit.RecurrenceActivityId) {
                this.recurringTitleAction = "Delete recurring slot"
                this.recurringAction = "delete"
                this.openRecurringModeModal()
            } else {
                this.deleteSlotCall(undefined)
            }
            

        } else {
            console.log('not in production')
            this.$modal.hide('createEditSlotModal');
            this.showLoader = null
            this.$store.dispatch('addEvents', this.$store.state.eventsToInsert);
        }
    },
    executeRecurringAction(mode) {
        switch (this.recurringAction) {
            case "delete":
                this.deleteSlotCall(mode)
                break;
            case "upsert":
                this.upsertSlotCall(mode)
                break;
            default:
                break;
        }
    },

    deleteSlotCall(mode) {
        // this.recurringTitleAction = null
        this.recurringAction = null
        let params = { availabilityId : this.slotToEdit.id, mode: mode };
            LCC.callApex(
                'OnlineAppointmentsController.deleteAvailability',
                JSON.stringify(params),
                (result, event) => {
                    this.showLoader = null
                    if (event.status) {
                         this.$store.dispatch('emptyEvents');
                        this.reloadAvailabilities(result)
                        this.$modal.hide('createEditSlotModal');
                        if (result.excludedSlots && result.excludedSlots.length > 0) {
                            this.$alert(
                            `${result.deletedAvailabilities && result.deletedAvailabilities.length > 0 ?`Your slot${result.deletedAvailabilities.length > 1 ?"s were" : " was"}  deleted except for the ones which already have appointments booked by clients: ` : `Your slot was not deleted because you already have appointments booked by clients` } ${result.excludedSlots.map((rec) => moment.tz(rec, this.storeInfos.Time_Zone_Id__c).format('LLL')).join(', ')}`,
                                '',
                                'warning'); 
                        }
                    } else if (event.type === 'exception') {
                        this.$alert(event.message + ' : ' + event.where,'','warning').then(() => {
                            this.$modal.hide('createEditSlotModal');
                        });
                        console.log(event.message + ' : ' + event.where)
                    } else {
                        this.$alert(event,'','warning').then(() => {
                            this.$modal.hide('createEditSlotModal');
                        });
                        console.log('Unknown Error', event)
                    }
                },
                {escape: false}
            )
    },

    upsertAvailability(){
        if (process.env.NODE_ENV === 'production') {
            if(this.chosenCapacity < (this.slotToEdit.definedCapacities -  this.slotToEdit.freeCapacities)) {
                this.$alert(
                        `You are not allowed to reduce the capacities of this slot to ${this.chosenCapacity}.`,
                        '',
                        'warning').then(() => {
                        this.showLoader = null
                        // this.$modal.hide('createEditSlotModal');
                        });
                        
                        return;
            }
            if(this.slotToEdit.RecurrenceActivityId) {
                this.recurringTitleAction = "Update recurring slot"
                this.recurringAction = "upsert"
                this.openRecurringModeModal()
            } else {
                this.showLoader = 'loading'
                this.upsertSlotCall(undefined)
            }

            

        } else {
        console.log('not in production')
            this.showLoader = null
            this.$modal.hide('createEditSlotModal');
            this.$store.dispatch('addEvents', this.$store.state.eventsToInsert);
            this.slotsToUpsert = []
        }
    },

    upsertSlotCall(mode) {
        // this.recurringTitleAction = null
        this.recurringAction = null
         let eventToUpdate  = this.slotsToUpsert[0];
         eventToUpdate.mode = mode;
         if (mode == "ALL" && this.recurrenceRefStartDateTime){
             let maxDate = moment(msToDate(this.recurrenceRefStartDateTime)).add(this.getMaxEndDayRecurrencesAllowed(), 'days');
             if(maxDate < this.end) {
                 eventToUpdate.RecurrenceEndDateOnly = maxDate
             }
         } 
        LCC.callApex(
            'OnlineAppointmentsController.updateAvailability',
            JSON.stringify(eventToUpdate),
            (result, event) => {
                this.showLoader = null
            if (event.status) {
                
                this.$store.dispatch('emptyEvents');
                this.reloadAvailabilities(result)
                this.$modal.hide('createEditSlotModal');
                if (result.excludedSlots && result.excludedSlots.length > 0) {
                    this.$alert(
                    `${result.updatedAvailabilities && result.updatedAvailabilities.length > 0 ?`Your slot${result.updatedAvailabilities.length > 1 ?"s were" : " was"} modified except for the ones which already have appointments booked by clients: ` : `Your slot was not modified because you already have appointments booked by clients ` } ${result.excludedSlots.map((rec) => moment.tz(rec, this.storeInfos.Time_Zone_Id__c).format('LLL')).join(', ')}`,
                        '',
                        'warning');
                }
            } else if (event.type === 'exception') {
                    this.$alert(event.message + ' : ' + event.where,'','warning').then(() => {
                        this.$modal.hide('createEditSlotModal');
                    });
                    console.log(event.message + ' : ' + event.where)
            } else {
                this.$alert(event,'','warning').then(() => {
                    this.$modal.hide('createEditSlotModal');
                });
                console.log('Unknown Error', event)
            }
            },
            {escape: false}
        )
        this.slotsToUpsert = []
    },
    msToDate(ms){
        return moment(ms).tz(this.storeInfos.Time_Zone_Id__c).format('YYYY-MM-DD');
    },
    getLocaleHour(datems){

        let localHour = null;
        if(datems){
            localHour = moment(datems).tz(this.storeInfos.Time_Zone_Id__c).format('HH');
        }
        return localHour
      },
      reloadAvailabilities(response) {
            utils.getDateRangeAvailabilities(false);
            utils.getOnlineAndTeamAppointments('range');
        },
 
  },
 computed:{
     selectedWeeklyDays:{
          get: function () {
            return this.selectedDayForRecurrence
          },
          set: function (newValue) {
            this.selectedDayForRecurrence = newValue; 
          }
      },
      selectedRadio: {
          get: function() {
              if (this.end) {
                  return 'on'
              } 
              return this.radios
          },

          set: function(newValue) {
              this.radios = newValue
          }
      },
      nowTime(){
        var d = new Date();
        return d;
      },
      capacityOptions(){
        var start = 0;
        var end = 20;

        var slot = this.slotToEdit;

        if(slot){
            var date = new Date(slot.localStoreStartDateTime);
            var date2 = new Date(slot.gmtStartDateTime);
            var formattedDate = date.getFullYear() + '-' + ("0" + (date.getMonth() + 1)).slice(-2) + '-' + ("0" + date.getDate()).slice(-2);
            var time = ("0" + date.getHours()).slice(-2);
            var time2 = ("0" + date2.getHours()).slice(-2);

            var alreadyBookedNB = this.$store.state.eventsMap[formattedDate][time2 + ':00'] ? this.$store.state.eventsMap[formattedDate][time2 + ':00'].length - 1 : 0;

            return Array(end - alreadyBookedNB + 1).fill().map((_, idx) => alreadyBookedNB + idx);
        } else {
            return Array.from({length: 20}, (_, i) => i);
        }
        
      },
      optionsHours(){
        let n = 24;
        let options = [];
        for (var i=0; i<n; i++) {
          if(i.toString().length == 1){
               i = '0' + i; 
          }
          options.push(i + ':00')
        } 
      return options;
      },
      storeInfos(){
        return this.$store.state.storeInfos;
      },
      isUserActionAuthorized(){
          return utils.isUserActionAuthorized();
      },
      isCSCUser(){
          return utils.isCSCUser();
      },
      showRecurrenceMenu(){
        var a = ("2017-05-02T" + this.timeFrom);
        var b = ("2017-05-02T" + this.timeTo);
        var milliseconds = ((new Date(a)) - (new Date(b)));
        var minutes = milliseconds / (60000);
        if(Math.abs(minutes) <= 5*60){
            return true;
        } else {
            return false;
        }
      },
    isFutureEvent() {
        return moment(this.slotToEdit.gmtStartDateTime) > moment().add(1, 'days')
    },
      
  } 
}
</script>

<style >
    .createEditSlotModal .v--modal-box.v--modal{
        min-width: 586px;
        margin: auto;
        left: unset !important;
        border-radius: 11px;
    }
    
    .createEditSlotModalContent{
        padding: 42px 31px 51px 48px;
        text-align: right;
        padding-top: 30px;
    }

    .modal-close{
        position: absolute;
        right: 2em;
        top: 2em;
        width: 1.1em;
        height: 1.1em;
        cursor: pointer;
    }

    .modal-close:after, .modal-close:before {
        content: '';
        position: absolute;
        width: 2px;
        height: 1.5em;
        background: #ccc;
        display: block;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        left: 50%;
        margin: -3px 0 0 -1px;
        top: 0;
    }

    .modal-close:hover:after,
    .modal-close:hover:before {
        background: #aaa;
    }

    .modal-close:before{
        transform: rotate(-45deg);
    }

    .inputs-container{
        padding: 0;
        padding-right: 40px
    }

    .modal-title{
        font-size: 27px;
        text-align: left
    }
    .eventTypeSelect{
        width: 100px;
        border: 1px solid black;
        padding-left: 10px;
    }
    .eventTypeBtn{
        background-color: #000000;
        color: #ffffff;
        font-size: 15px;
        min-width: 158px;
        min-height: 46px;
        outline: none;
        margin-top: 10px;
        margin-right: 12px
    }
    .delete-btn{
        background-color: #eae8e4;
        color: #000000;
        margin-right: 12px;
         padding: 12px 45px;
    }
    .inputsLabel{
        text-align: left;
        padding-top: 17px;
        opacity: 0.5;
        font-size: 18px;
        line-height: 1.22;
        color: #000000;
    }

    .layout.row {
        align-items: baseline;
        max-height: 70px;
    }

    .v-input__slot {
        min-height: 48px;
        background-color: #f6f5f3;
        padding-left: 19px;
    }

    .v-input__slot:before, .v-input__slot:after{
        display: none;
    }

    .v-text-field .v-input__append-inner{
        align-self: auto;
    }

    .capacityText{
        text-align: left;
        padding-left: 15px !important;
        padding-top: 25px !important;
    }

    .week-day-wrapper{
        display: flex;
        flex-wrap: nowrap
    }

    .week-day-wrapper::before{
        content: "Repeat on";
        position: absolute;
        top: 43%;
        color: #8c9199;
    }

    .radio-ends::before{
        content: "Ends";
        position: absolute;
        top: 61.5%;
        font-size: 14px;
        color: #8c9199;
        margin-bottom: 20px;
    }

    .Reccurence-select {
        margin-right: 10px;
        text-align: left;
    }

    /* .Reccurence-select::before{
        content: "Repeat every";
        position: absolute;
        top: 43%;
        color: #8c9199;
    } */

    .week-day button {
        padding: 2px 6px;
        height: 26px;
        width: 35px;
        min-width: unset;
        margin: auto 5px auto 0;
        box-shadow: none;
        border-radius: 5px;
        background-color: #f9f8f6;
        color: #949494;
        outline: none;
        cursor: pointer;
    }

    .selectedDay{
        background-color: #d9d7d2 !important;
        color: #000000 !important;
    }

    .v-radio {
        min-width: 57px;
    }

    .radio-ends .v-input__slot{
        padding-left: 0;
        background: none
    }

    .radio-ends-row{
        display: flex;
        height: 34px;
    }

    .radio-ends-row .v-input__slot{
        background-color: #f6f5f3;
        padding-left: 19px;
        min-height: 40px
    }

    .radio-ends-row .v-text-field{
        margin-top: -20px;
    }


    .radio-input .v-input__slot{
        width: 43px;
        padding-left: 12px;
    }

    label.v-label {
        color: #000000 !important;
        font-size: 17px;
    }

    .toLabel{
        margin: auto;
        opacity: 0.5;
        font-size: 18px;
    }

    .footer-btn{
        display: flex;
        justify-content: flex-end;
    }

    @media screen and (max-width: 1024px) {
      .createEditSlotModal .v--modal-box.v--modal{
        min-width: 510px;
        top: 60px !important;
        margin: auto !important;
        height: auto !important;
        left: 0 !important;
      }
      
      .createEditSlotModalContent {
        padding: 27px 16px 27px 33px;
      }

      .radio-ends::before{
        top: 59%;
      }
    }

    .weekDayBtn{
        border: 1px solid #adadad;
        border-radius: 5px;
        margin: 2px;
        width: 24px;
    }

        .lds-spinner {
    color:  #fff;
    /* display: inline-block; */
    position: relative;
    bottom: 16px;
    left: 60px;
    /* width: 80px;
    height: 80px; */
    }
    .lds-spinner div {
    transform-origin: 15px 17px;
    animation: lds-spinner 1.2s linear infinite;
    }
    .lds-spinner div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 14px;
    width: 2px;
    height: 9px;
    border-radius: 20%;
    background: #fff;
    }
    .lds-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
    }
    .lds-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
    }
    .lds-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
    }
    .lds-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
    }
    .lds-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
    }
    .lds-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
    }
    .lds-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
    }
    .lds-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
    }
    .lds-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
    }
    .lds-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
    }
    .lds-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
    }
    .lds-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
    }
    @keyframes lds-spinner {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
    }
</style>


