<template>
    <div class="user-selection-container">
      <div class="user-selection-layout" v-if="shouldShowModal" @click.stop="closePopup"></div>
      <div class="user-selection-content">
        <button class="user-selection-btn btn-all" :class="getClickedClass('All')" @click="setFilterType('All')">All Appointments</button>
        <button class="user-selection-btn btn-team" :class="getClickedClass('Team')" @click.stop="openPopup">My Team</button>
        <div class="user-selection-icon" v-if="shouldShowModal">
          <div class="user-selection-icon-box">
            <!-- <template v-if="availableTeamUsers && availableTeamUsers.length > 0"> -->
              <template v-for="user in availableTeamUsers">
                <div class="icon-content" :key="Math.random() + user.Id">
                  <div style="display: flex;align-items: center;">
                    <div class="icon-content-picture"><img class="userImage"  :src="user.FullPhotoUrl ? user.FullPhotoUrl : null" /><!--<v-icon>person</v-icon>--></div>
                    <div class="icon-content-name">{{user.FirstName}} {{user.LastName}}</div>
                  </div>
                  <button :id="user.Id + Math.random()" class="icon-content-btn" @click="userSelection(user)" :class="{Clicked:selectedUsers.includes(user.Id)}">
                    <v-icon>mdi-check</v-icon>
                  </button>
                </div>
              </template>
            <!-- </template> -->
          </div>
        </div>
      </div>
      <div class="user-selection-content middle-box">
        <button class="user-selection-btn btn-available" :class="getClickedClass('Available')" @click="setFilterType('Available')">Slots</button>
        <button class="user-selection-btn btn-unassigned" :class="getClickedClass('Unassigned')" @click="setFilterType('Unassigned')">Unassigned</button>
        <button class="user-selection-btn btn-assigned" :class="getClickedClass('Assigned')" @click="setFilterType('Assigned')">Assigned</button>
        <span class="user-selection-online">online</span>
      </div>
      <div class="user-selection-content">
        <button class="user-selection-btn btn-icon" :class="getClickedClass('ICON')" @click="setFilterType('ICON')">ICON Appointments</button>
      </div>   
    </div>
</template>

<script>

// import Vue from 'vue'

// Vue.directive('click-outside',{
//   bind: function (el, binding, vnode) {
//       el.eventSetDrag = function () {
//           el.setAttribute('data-dragging', 'yes');
//       }
//       el.eventClearDrag = function () {
//           el.removeAttribute('data-dragging');
//       }
//       el.eventOnClick = function (event) {
//           var dragging = el.getAttribute('data-dragging');
//           // Check that the click was outside the el and its children, and wasn't a drag
//           if (!(el == event.target || el.contains(event.target)) && !dragging) {
//               // call method provided in attribute value
//               vnode.context[binding.expression](event);
//           }
//       };
//       document.addEventListener('touchstart', el.eventClearDrag);
//       document.addEventListener('touchmove', el.eventSetDrag);
//       document.addEventListener('click', el.eventOnClick);
//       document.addEventListener('touchend', el.eventOnClick);
//   }, unbind: function (el) {
//       document.removeEventListener('touchstart', el.eventClearDrag);
//       document.removeEventListener('touchmove', el.eventSetDrag);
//       document.removeEventListener('click', el.eventOnClick);
//       document.removeEventListener('touchend', el.eventOnClick);
//       el.removeAttribute('data-dragging');
//   },
// });

export default {
  name: 'UsersSelection',
  components: {
    
  },
  data () {
    return {
      shouldShowModal: false  
    }
  },
  computed: {
      availableTeamUsers(){
        //sort tem members list alphabetically
          return this.$store.state.availableTeamUsers.sort(function(a, b){
              if(a.FirstName < b.FirstName) { return -1; }
              if(a.FirstName > b.FirstName) { return 1; }
              return 0;
          });
      },
      selectedEventsFilters(){
        return this.$store.state.selectedEventsFilters;
      },
      selectedUsers(){
        return this.$store.state.selectedUsers;
      },
  },
  methods:{
    
    makeDisplay: function() {
          this.isActive = !this.isActive;
    },
    openPopup() {
      this.shouldShowModal = !this.shouldShowModal;
    },
    closePopup() {
      this.shouldShowModal = false;
    },
    setFilterType(info){
      this.$store.dispatch('setSelectedEventsFilters', info)
    },
    getClickedClass(filter){
      return this.selectedEventsFilters.indexOf(filter) != -1 ? 'selectedFilter' : null;      
    },
    userSelection(user){
      // console.log('user id selected', user)
      if(!this.selectedEventsFilters.includes('Team')){
        this.$store.dispatch('setSelectedEventsFilters', 'Team')
      }
      this.$store.dispatch('setSelectedUsers', user)
      if(this.selectedUsers.length == 0){
        this.$store.dispatch('setSelectedEventsFilters', 'Team')
      }
    },
  },
  mounted(){
    // this.setFilterType('All')    
  },
}
</script>
<style>
    .user-selection-container{
      background-color: #f2f3f7;
      padding: 13px;
      display: flex;
      justify-content: center;
    }

    .user-selection-layout {
      position: absolute;
      width: 100%;
      height: 100%;
      background: transparent;
      top: 0;
      z-index: 99;
      bottom: 0;
      left: 0;
    }

    .user-selection-content{
      padding: 16px 13px;
      background-color: #ebedf5;
      border-radius: 4px;
      border: solid 1px #edeeef;
      display: flex;
      position: relative;
      margin-right: 12px;
    }

    .middle-box{
      border: 1px solid rgba(118, 129, 145, 0.27);
    }

    .user-selection-btn{
      border-radius: 18.5px;
      box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.25);
      padding: 5px 20px 5px;
      background-color: #ffffff;
      font-size: 16px;
      outline: none;
      white-space: nowrap;
    }

    .btn-all{
      border: solid 1px rgba(46, 58, 73, 0.41);
    }

    .btn-all.selectedFilter{
      background: #000000;
      color: #ffffff;
    }

    .btn-team{
      color: #54698d;
      margin-left: 11.2px;
    }

    .btn-team.selectedFilter{
      background: #54698d;
      color: #ffffff;
    }

    .btn-available{
      border: solid 1px #7a91ff;
      color: #7a91ff;
    }

    .btn-available.selectedFilter{
      background: #7a91ff;
      color: #ffffff;
    }

    .btn-unassigned{
      border: solid 1px #e30041;
      color: #e30041;
      margin-left: 11.2px;
    }

    .btn-unassigned.selectedFilter{
      background: #e30041;
      color: #ffffff;
    }

    .btn-assigned{
      border: solid 1px #81db8d;
      color: #40ba50;
      margin-left: 11.2px;
    }

    .btn-assigned.selectedFilter{
      background: #81db8d;
      color: #ffffff;
    }

    .btn-icon{
      border: solid 1px #bead8a;
      color: #bead8a; 
    }

    .btn-icon.selectedFilter{
      background: #bead8a;
      color: #ffffff;
    }

    .user-selection-online {
      position: absolute;
      right: 42%;
      border-radius: 5px;
      bottom: 85%;
      background-color: #f2f3f7;
      line-height: 1.3;
      padding: 0px 5px 1px;
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 500;
      color: #6781fb;
    }

    .Display{
      display: none
    }

    .user-selection-icon{
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.09);
      border: solid 1px #e5e5e5;
      background-color: #ffffff;
      position: absolute;
      z-index: 100;
      top: 95%;
      padding-bottom: 10px;
      min-width: 360px;
      left: -30%;
      
    }

    .user-selection-icon-box{
      padding: 16px 18px 8px;
      max-height: 268px;
      overflow-y: auto
    }

    .user-selection-icon:after, .user-selection-icon:before {
      bottom: 100%;
      right: 15%;
      border: solid #e5e5e5 1px;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
    }

    .user-selection-icon:after {
      border-color: rgba(255, 255, 255, 0);
      border-bottom-color: #ffffff;
      border-width: 6px;
      margin-left: -10px;
    }
    .user-selection-icon:before {
      border-color: rgba(255, 255, 255, 0);
      border-bottom-color: #fff;
      border-width: 6px;
      margin-left: -11px;
    }

    .icon-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 13px;
    }

    .icon-content-btn{
      width: 25px;
      height: 25px;
      border-radius: 100%;
      background-color: #f1f1f1;
      color: #969696;
      outline: none;
    }

    .Clicked{
      background-color: #000000;
      color: #ffffff
    }

    .icon-content-btn .v-icon{
      font-size: 17px;
      color: inherit
    }

    .icon-content-name{
      font-size: 17px;
      color: #000000;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-weight: 500;
      max-width: 255px;
      overflow: hidden;
      margin-left: 10px;
    }

    /* .icon-content-name::before{
      background-color: grey;
      width: 25px;
      height: 25px;
      border-radius: 100%;
      content: '';
      display: inline-block;
      top: 6px;
      position: relative;
      margin-right: 15px;
    } */

    .icon-content-picture{
        width: 20px;
        margin-right: 15px;
    }

    .userImage{
      width: 30px;
      height: 30px;
      border-radius: 100%; 
      filter: grayscale(0.5);
    }

    @media screen and (max-width: 1024px) {

      .user-selection-btn {
        padding: 4px 10px 4px;
        font-size: 13px;
      }

      .user-selection-content {
        padding: 16px 5px;
      }

      .user-selection-icon {
        min-width: 300px;
        left: 0px;
        right: 0px;
      }

      .user-selection-icon:after, .user-selection-icon:before {
        right: 45%;
        left: unset;
      }

      .user-selection-online {
        line-height: 1.2;
        font-size: 10px;
      }

    }

    @media screen and (max-width: 768px) {
      .user-selection-content {
        padding: 16px 5px;
      }

      .user-selection-btn {
        padding: 3px 6px 3px;
        font-size: 11px;
      }

    }

    

</style>


