<template>

  <div class="leftMenu-wrapper" :class="{Transition: isTransition}">

    <div class="leftMenu" >
        <!-- <img v-if="storeInfos.Name == 'LV CHAMPS ELYSEES'" class="storeImage"  src="@/assets/lv-champs-elysees.jpg" />  -->      <!-- TODO call proper table to get Store's image -->
        <img class="storeImage" style="width: 150px;height: auto;" src="@/assets/LV-logo.png" />
        <span class="storeName">{{storeInfos.Name ? decodeURI(storeInfos.Name) : 'Choose a store'}}</span>

        <div style="display: flex;justify-content: space-evenly;" v-if="isVirtualEnabled">
          <h2 v-show="storeAPtType.indexOf('inStore') != -1" class="virtual_label-box">In Store</h2>
          <h2 v-show="storeAPtType.indexOf('distant') != -1" class="virtual_label-box">Virtual</h2>
        </div>

        <hr class="hrSeparator" />

        <div class="today-date">
          <div class="today-weekday">{{todayWeekday}}</div>
          <div class="middle-date">
            <div>{{todayDay}}</div>&nbsp;
            <div>{{todayMonth}}</div>
          </div>
          <div class="today-year">{{todayYear}}</div>

          <div class="appointments-num">
            Hello{{userInfos.FirstName ? (userInfos.FirstName.includes('Store') ? '' : ', ') : ', '}}<span style="font-weight: 600">{{userInfos.FirstName ? (userInfos.FirstName.includes('Store') ? '' : userInfos.FirstName) : 'user'}} !</span> 
            You have <span style="font-weight: 600">{{unassignedInMonth ? unassignedInMonth.length : 0}}</span><br> unassigned appointments
          </div>

        </div>

        <hr class="hrSeparator" />

        <template >
          <div justify="center" color="black" class="date-picker">
            <v-date-picker 
              v-model="datePicker" 
              color="black" 
              width="100%" 
              @change="miniCalChange" 
              :events="unassignedInMonth" 
              :event-color="'red'" 
              prev-icon="mdi-chevron-left"
              next-icon="mdi-chevron-right"
            ></v-date-picker>
          </div>
        </template>

    </div>
    
     <div class="back-btn-container" >
      <v-btn
        absolute
        class="back-btn"
        @click="makeTransition" 
        >
         <v-icon size="20px" v-if="!isTransition">mdi-arrow-left</v-icon>
         <v-icon size="20px" v-if="isTransition">mdi-arrow-right</v-icon>
       </v-btn>
       <v-btn
        absolute
        class="refresh-btn"
        @click="reloadEvents"
        >
         <v-icon size="20px">mdi-refresh</v-icon>
       </v-btn>

       
       <v-btn
        class="store-hierarchy-btn"
        absolute
        v-if="storeChangeAuthorized"
        @click="showStoreHierarchyModal"
        >
         <v-icon size="20px">mdi-home-city-outline</v-icon>
       </v-btn>

       <!-- <v-btn
        class="store-settings-btn"
        absolute
        v-if="appointmentTypeChangeAuthorized"
        @click="showCurrentStoreSettingsModal"
        >
         <v-icon size="20px">mdi-settings</v-icon>
       </v-btn> -->
       
       <v-btn
        class="store-settings-btn"
        absolute
        v-if="virtualStoreAndProfileCheck"
        @click="showCurrentStoreSettingsModal"
        >
         <v-icon size="20px">mdi-settings</v-icon>
       </v-btn>


       <!-- <v-btn
        absolute
        class="store-hierarchy-btn"
        @click="$emit('calKeyUpdate')"
       >
         <v-icon size="20px">mdi-refresh</v-icon>
       </v-btn> -->

       
    </div>

    
    <StoreHierarchyModal />
    <CurrentStoreSettingsModal />

  </div>
</template>

<script>
import utils from '../commons/utils';
import StoreHierarchyModal from '../components/StoreHierarchyModal';
import CurrentStoreSettingsModal from '../components/CurrentStoreSettingsModal';

export default {
  name: 'LeftMenu',
  components: {
    StoreHierarchyModal,
    CurrentStoreSettingsModal,
  },
  data () {
    return {
      todayDay : new Date().getDate(),
      todayYear : new Date().getFullYear(),
      isHidden: false,
      isTransition: false,
      datePicker: '',
      images: {
        sample: require('../assets/lv-champs-elysees.jpg')
      },
      miniCalEvents: [],
    }
  },
  computed: {
    storeAPtType(){
      let currentStore = this.storeInfos;
      let storeAPtType  =  currentStore.AppointmentType__c ? currentStore.AppointmentType__c.split(";") : 'inStore'; 
      return storeAPtType;
    },
    todayWeekday(){
      let date = new Date();
      let realDate = new Date().toLocaleString('en-us', {  weekday: 'long' });
      return realDate
    },
    todayMonth(){
      let date = new Date();
      let realMonth = new Date().toLocaleString('en-us', {  month: 'long' });
      return realMonth
    },
    storeInfos(){
      return this.$store.state.storeInfos;
    },
    userInfos(){
      return this.$store.state.userInfos;
    },
    unassignedInMonth(){
      let unassignedEvents = [];
      for(var i=0; i<this.$store.state.events.length; i++){
        if(this.$store.state.events[i].status == 'Not Assigned'){
          unassignedEvents.push(this.msToDate(this.$store.state.events[i].localStoreStartDateTime));
        }
      }
      return unassignedEvents;
    },
    storeChangeAuthorized(){
      return utils.storeChangeAuthorized();
    },
    appointmentTypeChangeAuthorized(){
      return utils.appointmentTypeChangeAuthorized();
    },
    isVirtualEnabled(){
      return this.$store.state.isVirtualEnabled;
    },
    virtualStoreAndProfileCheck(){
      return this.appointmentTypeChangeAuthorized && this.isVirtualEnabled ? true : false;
    }
  },
  methods:{
    miniCalChange(e){
      // console.log(e)
      let actualRange= {};

      actualRange.start = e;
      actualRange.end = e;
      actualRange.startDisplayString = this.dateToDisplayString(new Date(e.split('-')[0], parseInt(e.split('-')[1]) - 1, e.split('-')[2]));
      actualRange.endDisplayString = this.dateToDisplayString(new Date(e.split('-')[0], parseInt(e.split('-')[1]) - 1, e.split('-')[2]));
      actualRange.year = e.split('-')[0];

      this.$store.dispatch('setCalendarStart', e);   

      //this.$store.dispatch('updateCalendarViewType', 'day');   
      this.$store.dispatch('updateActualRange', actualRange);
      utils.getDateRangeAvailabilities(false)
    },
    dateToDisplayString(date){
      let month = date.toLocaleString('en-GB', { month: 'long' });
      return month + ' ' + date.getDate()
    },
    msToDate(ms) {
        var d = new Date(ms);
        return d.getFullYear() + '-' + ("0" + (d.getMonth() + 1)).slice(-2) + '-' + ("0" + d.getDate()).slice(-2);
    },
    makeTransition: function() {
      this.isTransition = !this.isTransition;
    },
    reloadEvents(){
      this.$store.dispatch('emptyEvents');
      utils.getDateRangeAvailabilities(false);
      utils.getOnlineAndTeamAppointments('month');
    },
    showStoreHierarchyModal(){
      this.$modal.show('StoreHierarchyModal')
    },
    showCurrentStoreSettingsModal(){
      this.$modal.show('CurrentStoreSettingsModal')
    },
  }
}
</script>
<style>

  .Transition{
    margin-left: -282px;
  }

  .leftMenu-wrapper {
    transition: all 500ms;
    display: flex;
    width: 323px;
    box-shadow: 19px 2px 22px -9px rgba(25, 17, 11, 0.13);
    border-right: solid 1px #eae8e4;
    z-index: 40;
    text-align: center;
    background-color: #ffffff;
}

.back-btn-container {
    right: 41px;
    height: auto;
    box-shadow: none;
    border: none;
    transition: all 500ms;
    position: relative;
}

.leftMenu {
    width: auto;
    padding: 0 41px;
    float: none;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    max-height: 100vh;
    overflow-x: hidden;
}
.leftMenu::-webkit-scrollbar {
    height: 0;
    width: 0; /* Remove scrollbar space */
    background: transparent;
}

.virtual_label-box {
    border-radius: 4px;
    width: 62px;
    height: 24px;
    text-align: center;
    font-size: 15px;
    border: 1px solid;
    border-color: black;
    text-transform: capitalize;
    line-height: 24px;
  }

